.delivery {
  margin-top: 70px;
  display: flex;
  justify-content: space-between;
  h2 {
    color: #2a2a2a;
    font-family: "BerlingskeSlab-XBd";
    font-size: 40px;
    font-weight: 400;
    line-height: 65px;
    letter-spacing: 0.8px;
    margin-bottom: 50px;
    span {
      font-family: "BerlingskeSlab-DBdItalic";
      line-height: 70px;
      color: #ffffff;
      position: relative;
      display: block;
      width: fit-content;
      padding-left: 10px;
      &:after {
        content: "";
        width: 338px;
        height: 74px;
        background-color: #009ee0;
        background-image: linear-gradient(0deg,#40adff 0,#78c5ff);
        position: absolute;
        transform: translate(-50%,-50%) skewX(-6deg);
        left: 53%;
        top: 50%;
        border-radius: 3px;
        z-index: -1;
      }
    }
  }

  &__note {
    color: #353535;
    font-family: "BerlingskeSlab-XBd";
    font-size: 22px;
    font-weight: 400;
    line-height: 32px;
    letter-spacing: 0.44px;
    position: relative;
    max-width: 310px;
    margin-bottom: 80px;
  }

  &__info {
    display: flex;
    align-items: center;
    margin-left: -22px;
    position: relative;
    z-index: 5;
    img {
      margin-right: 13px;
    }

    p {
      max-width: 350px;
      span {
        color: #535353;
        font-family: "BerlingskeSlab-Md";
        font-size: 18px;
        font-weight: 400;
        letter-spacing: 0.36px;

        &:first-of-type {
          color: #444444;
          font-family: "BerlingskeSlab-DBdItalic";
          display: block;
          margin-bottom: 7px;
        }
      }
    }
  }

  &__aside {
    margin-right: 115px;
    width: 331px;
    height: 197px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    p {
      color: #535353;
      font-family: "BerlingskeSlab-Md";
      font-size: 18px;
      font-weight: 400;
      line-height: 28px;
      letter-spacing: 0.36px;
      text-align: center;
      max-width: 205px;
      margin: 20px auto 0;
    }

    &-bg {
      position: absolute;
      left: -74px;
      top: -80px;
      z-index: -1;
    }
  }
}

@media screen and (max-width: 1240px) {
  .delivery {
    &__main {
      padding-left: 20px;
      position: relative;
      z-index: 10;
    }
  }
}

@media screen and (max-width: 1140px) {
  .delivery {
    &__aside {
      margin-right: 0;
    }
  }
}

@media screen and (max-width: 992px) {
  .delivery {
    flex-direction: column;
    h2 {
      text-align: center;
      span {
        margin: auto;
      }
    }
    &__note {
      margin: 0 auto 60px;
    }
    &__info {
      justify-content: center;
    }
    &__aside {
      margin: 70px auto 0;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        z-index: -2;
        left: -1%;
        top: 50%;
        transform: translate(-50%, -50%);
        background-image: url(../img/delivery_mobile_map.png);
        background-size: 1067px;
        background-position: center;
        height: 645px;
        width: 1067px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .delivery {
    h2 {
      font-size: 32px;
      line-height: 40px;
      margin-bottom: 25px;
      span:after {
        width: 289px;
        height: 57px;
      }
    }
    &__note {
      font-size: 18px;
      line-height: 24px;
      max-width: 250px;
    }
  }
}

@media screen and (max-width: 600px) {
  .delivery {
    h2 {
      font-size: 20px;
      font-weight: 400;
      line-height: 8px;
      margin-bottom: 7px;

      span:after {
        width: 197px;
        height: 45px;
      }
    }
    &__note {
      font-size: 12px;
      font-weight: 400;
      line-height: 20px;
      max-width: 170px;
    }
    &__info {
      img {
        display: block;
        width: 100%;
        max-width: 110px;
      }
      p {
        span {
          font-size: 12px;
          line-height: 20px;
          &:first-of-type {
            margin-bottom: 0;
          }
        }
      }
    }
    &__aside {
      margin: 45px auto 0;
      &:after {
        left: -43%;
        top: 51%;
      }
      p {
        font-size: 14px;
        line-height: 22px;
      }
    }
    &__aside-bg {
      top: -58px;
      left: 55%;
      transform: translateX(-50%);
      img {
        width: 490px;
      }
    }
    .hover_info_ico {
      width: 42px;
      height: 42px;
    }
  }
}