.default-input {
  max-width: 360px;
  margin-left: auto;
  margin-right: auto;
  display: block;
  border-radius: 10px;
  background-color: #f3f3f3;
  padding: 28px 34px;
  color: #535353;
  font-family: "BerlingskeSlab-Lt";
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.3px;
  margin-bottom: 22px;
  border: 1px solid #f3f3f3;
  transition: .2s ease;
  &:focus {
    background-color: #fff;
  }
  &.error {
    border-color: red;
  }

  @media screen and (max-width: 600px) {
    padding: 22px;
    font-size: 13px;
  }
}