.btn {
  box-shadow: 0 5px 0 #c04827;
  border-radius: 10px;
  background-color: #fedd69;
  background-image: linear-gradient(to top, #f35e36 0%, #f37636 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 1;
  span {
    color: #ffffff;
    font-family: "BerlingskeSlab-XBd";
    
    font-weight: 400;
    letter-spacing: 0.44px;
  }
  &:after {
    content: '';
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to top, #f56a44 0%, #fa884f 100%);
    border-radius: 10px;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
    opacity: 0;
    transition: .2s linear;
  }
  &:hover {
    &:after {
      opacity: 1;
    }
  }
}