.get {
  padding-top: 115px;
  position: relative;

  .container {
    position: relative;
  }
  
  & > img {
    // display: none;
    left: 0;
    top: -370px;
    position: absolute;
    pointer-events: none;
  }

  &__form {
    max-width: 630px;
    margin-left: auto;
    position: relative;
    padding: 87px 72px 78px;
  }

  &__form-bg {
    position: absolute;
    top: -69px;
    left: -86px;
    z-index: 0;
  }
  &__form-inner {
    position: relative;
    z-index: 2;
  }

  &__form-header {
    text-align: center;
    margin-bottom: 28px;
    h2 {
      color: #2a2a2a;
      font-family: "BerlingskeSlab-DBdItalic";
      font-size: 30px;
      font-weight: 400;
      line-height: 40px;
      letter-spacing: 0.6px;
    }
    span {
      font-family: "BerlingskeSlab-XBd";
      display: block;
    }
  }

  &__form-cta {
    display: flex;
    align-items: center;
    margin-bottom: 40px;
    max-width: 420px;
    margin-left: auto;
    margin-right: auto;
    img {
      margin-right: 18px;
    }
    p {
      color: #535353;
      font-family: "BerlingskeSlab-Md";
      font-size: 18px;
      font-weight: 400;
      line-height: 28px;
      letter-spacing: 0.36px;
    }
  }

  .dropzone {
    padding: 25px 25px 25px 34px;
    border-radius: 10px;
    border: 1px dashed #9b9b9b;
    background-color: #f2f7ff;
    min-height: auto;
    max-width: 360px;
    margin-left: auto;
    margin-right: auto;

    .dz-message {
      margin: 0;
      display: flex;
      justify-content: space-between;
      color: #535353;
      font-family: "BerlingskeSlab-Lt";
      font-size: 15px;
      font-weight: 400;
      line-height: 28px;
      letter-spacing: 0.3px;
    }

    .dz-preview {
      display: flex;
      width: 100%;
      background: transparent;
      margin: 0;
      min-height: auto;
      align-items: center;
      margin-top: 5px;

      * {
          font-family: 'BerlingskeSlab-Md';
      }
    }

    .dz-image, .dz-progress, .dz-error-message, .dz-success-mark, .dz-error-mark {
      display: none!important;
    }

    .dz-size span {
      padding-left: 0!important;
      background: none;
      white-space: nowrap;
    }

    .dz-details {
      opacity: 1;
    width: 97%;
    width: auto;
    padding: 0;
    display: flex;
    position: relative;
    min-width: auto;
    height: 20px;
    }

    .dz-remove {
      display: block;
    position: relative;
    width: 79px;
    height: 17px;
    z-index: 1;
    left: auto;
    transform: none;
    height: 20px;
    line-height: 20px;
    }
  }
  .dropzone .dz-preview .dz-details .dz-filename span, .dropzone .dz-preview .dz-details .dz-size span {
    background: none!important;
  }

  .dropzone .dz-preview .dz-details .dz-filename {
    max-width: 125px;
    text-overflow: ellipsis!important;
    overflow: hidden!important;
  }

  .btn {
    width: 360px;
    height: 95px;
    font-size: 19px;
    font-weight: 400;
    line-height: 26px;
    letter-spacing: 0.38px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 35px;
    margin-bottom: 31px;
    span {
      font-family: "BerlingskeSlab-Md";
    }
    b {
      font-family: "BerlingskeSlab-XBd";
      display: block;
    }
  }

  &__acception {
    width: 258px;
    margin-left: auto;
    margin-right: auto;
    color: #3a3a3a;
    font-family: "BerlingskeSlab-Lt";
    font-size: 14px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0.28px;
    a {
      color: #32a7ff;
      font-family: "BerlingskeSlab-LtItalic";
      text-decoration: underline;
      transition: .2s ease;
      &:hover {
        text-decoration-color: transparent;
      }
    }
  }

  .file_upload {
    left: 94px;
    bottom: 180px;
  }

  &__bg_mobile {
    display: none;
  }
}

@media screen and (max-width: 992px) {
  .get {
    padding-top: 350px;

    &>img {
      top: -40px;
      width: 100%;
    }

    .file_upload {
      bottom: auto;
    top: -152px;
    }

    &__form {
      margin: auto;
    }
  }
}

@media screen and (max-width: 768px) {
  .get {
    &__form {
      padding: 68px 30px 90px;
    }
    & > img {
      width: 800px;
    }
  }
}

@media screen and (max-width: 600px) {
  .get {
    padding-top: 400px;
    & > img {
      width: 610px;
      height: 482px;
    }

    .file_upload {
      top: -350px;
      left: 166px;
    }

    .container {
      padding: 0;
    }
    &__form {
      box-shadow: 0 23px 82px 3px rgba(93, 93, 93, 0.08);
      background-color: #ffffff;
      padding: 50px 16px;
      &:after {
        content: '';
        width: 100%;
        height: calc(100% - 14px);
        position: absolute;
        top: 7px;
        left: 0;
        bottom: 7px;
        right: 0;
        border-top: 1px solid #f3f3f3;
        border-bottom: 1px solid #f3f3f3;
      }
    }
    &__form-bg {
      display: none;
    }
    &__form-header {
      margin-bottom: 15px;
      h2 {
        font-size: 20px;
        line-height: 30px;
      }
    }
    &__form-cta {
      margin-bottom: 28px;
      img {
        margin-right: 7px;
      }
      p {
        font-size: 12px;
        font-weight: 400;
        line-height: 20px;
      }
    }

    .default-input {
      margin-bottom: 14px;
    }

    .dropzone {
      padding: 20px 25px 20px 34px;
      .dz-message {
        font-size: 13px;
      }
    }

    .btn {
      font-size: 15px;
      max-width: 360px;
      width: 100%;
      margin-top: 15px;
      margin-bottom: 25px;
      height: 80px;
      line-height: 22px;
    }

    &__acception {
      font-size: 14px;
    }

    &__bg_mobile {
      display: block;
      position: absolute;
      bottom: 0;
      right: -174px;
    }
  }
}