.team {
  margin-top: 90px;

  &__header {
    color: #2a2a2a;
    font-family: "BerlingskeSlab-XBd";
    font-weight: 400;
    letter-spacing: 0.8px;
    line-height: 65px;
    text-align: center;
    h2 {
      font-size: 40px;
    }
    span {
      display: block;
      width: fit-content;
      text-align: center;
      margin: auto;
      color: #ffffff;
      font-family: "BerlingskeSlab-DBdItalic";
      position: relative;
      &:after {
        content: '';
        width: 644px;
        height: 74px;
        background-color: #009ee0;
        background-image: linear-gradient(0deg,#40adff,#78c5ff);
        position: absolute;
        transform: translate(-50%,-50%) skewX(-8deg);
        left: 50%;
        top: 50%;
        border-radius: 3px;
        z-index: -1;
      }
    }
  }
  &__features {
    display: flex;
    justify-content: space-between;
    margin-top: 100px;
  }
  &__feature {
    flex-basis: 31.11%;
    margin-right: 2.22%;
    padding: 41px 0 44px 39px;
    box-shadow: 0 23px 140px 11px rgba(93, 93, 93, 0.06);
    border-radius: 4px;
    background-color: #fff;
    border: 8px solid #f1f1f1;
    position: relative;
    &:last-of-type {
      &:after {
        content: "";
        position: absolute;
        width: 72%;
        height: 102%;
        display: block;
        background-image: url(../img/team_feature_decor.png);
        background-repeat: no-repeat;
        right: -1px;
        bottom: -3px;
      }
    }
    &-icon {
      position: absolute;
      width: 83px;
      height: 83px;
      box-shadow: 0 0 28px 2px rgba(243, 97, 54, 0.09);
      background-color: #f37636;
      background-image: linear-gradient(to top, #f35e36 0%, #f37636 100%);
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      left: 40px;
      top: -44%;
    }
    p {
      color: #535353;
      font-family: "BerlingskeSlab-Md";
      font-size: 18px;
      font-weight: 400;
      line-height: 28px;
      letter-spacing: 0.36px;
    }
    &:nth-of-type(3n+3) {
      margin-right: 0;
    }
  }
  &__wrapper {
    display: flex;
    height: 470px;
    position: relative;
    &:before {
      content: '\00a0';
      position: absolute;
      top: -60px;
      left: 50%;
      transform: translateX(-50%);
      opacity: 0.05;
      color: #7b7884;
      font-family: "BerlingskeSlab-Blk";
      font-size: 199px;
      font-weight: 400;
      line-height: 305.57px;
      letter-spacing: 0.3px;
    }
  }
  &__member {
    position: relative;
    padding-top: 325px;
    padding-bottom: 60px;
    flex-basis: 16.666%;
    img {
      position: absolute;
      top: -40px;
      left:-50px;
    }

    &:nth-of-type(1) {
      .team__member-info {
        span {
          &:nth-of-type(2)  {
            &:after {
              transform: skew(-3deg);
              left: -37px;
              top: -15px;
              width: 148%;
              height: 154%;
            }
          }
        }
      }
    }
    &:nth-of-type(2) {
      img {
        left: -80px;
      }
      .team__member-info {
        margin-left: 30px;
        span {
          &:nth-of-type(1)  {
            &:after {
              transform: skew(-3deg);
              left: -37px;
              top: -15px;
            }
          }
          &:nth-of-type(2)  {
            &:after {
              transform: skew(-3deg);
              left: -37px;
              top: -15px;
              width: 138%;
              height: 154%;
            }
          }
        }
      }
    }
    &:nth-of-type(3) {
      img {
        left: -60px;
      }
      .team__member-info {
        margin-left: 70px;
        span {
          &:nth-of-type(1)  {
            &:after {
              transform: skew(-3deg);
              left: -37px;
              top: -15px;
            }
          }
          &:nth-of-type(2)  {
            &:after {
              transform: skew(-3deg);
              left: -23px;
              top: -16px;
              width: 154%;
              height: 212%;
            }
          }
        }
      }
    }
    &:nth-of-type(4) {
      img {
        left: -140px;
      }
      .team__member-info {
        margin-left: 40px;
        span {
          &:nth-of-type(1)  {
            &:after {
              transform: skew(-3deg);
              left: -37px;
              top: -15px;
            }
          }
          &:nth-of-type(2)  {
            &:after {
              transform: skew(-3deg);
              left: -23px;
              top: -16px;
              width: 154%;
              height: 212%;
            }
          }
        }
      }
    }
    &:nth-of-type(5) {
      img {
        left: -90px;
      }
      .team__member-info {
        margin-left: 40px;
        span {
          &:nth-of-type(1)  {
            &:after {
              transform: skew(-3deg);
              left: -37px;
              top: -15px;
            }
          }
          &:nth-of-type(2)  {
            &:after {
              transform: skew(-3deg);
              left: -23px;
              top: -16px;
              width: 154%;
              height: 212%;
            }
          }
        }
      }
    }
    &:nth-of-type(6) {
      img {
        left: -110px;
      }
      .team__member-info {
        margin-left: 40px;
        span {
          &:nth-of-type(1)  {
            &:after {
              transform: skew(-3deg);
              left: -37px;
              top: -15px;
            }
          }
          &:nth-of-type(2)  {
            &:after {
              transform: skew(-3deg);
              left: -37px;
              top: -15px;
              width: 138%;
              height: 154%;
            }
          }
        }
      }
    }
  }
  &__member-info {
    position: relative;
    z-index: 1;
    span {
      display: block;
      position: relative;
      width: fit-content;
      &:first-of-type {
        color: #ffffff;
        font-family: "BerlingskeSlab-DBdItalic";
        font-size: 18px;
        font-weight: 400;
        line-height: 23px;
        letter-spacing: 0.36px;
        z-index: 2;
        &:after {
          content: "";
          width: 141px;
          height: 54px;
          background-color: #009ee0;
          background-image: linear-gradient(0deg,#40adff,#78c5ff);
          position: absolute;
          transform: translate(-50%,-50%) skewX(-3deg);
          left: 50%;
          top: 50%;
          border-radius: 1px;
          z-index: -1;
        }
      }
      &:nth-of-type(2) {
        color: #000000;
        font-family: "BerlingskeSlab-DBdItalic";
        font-size: 15px;
        font-weight: 400;
        line-height: 23px;
        letter-spacing: 0.3px;
        margin-top: 22px;
        margin-left: 5px;
        white-space: nowrap;
        &:after {
          content: "";
          width: 120%;
          height: 130%;
          background-color: #fff;
          position: absolute;
          transform: translate(-50%,-50%) skewX(-3deg);
          left: 50%;
          top: 50%;
          border-radius: 1px;
          z-index: -1;
        }
      }
    }
  }

  .slick-dots {
    list-style-type: none;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    padding: 0;
    margin-top: 15px;
    position: absolute;
    bottom: 13px;
    left: 50%;
    transform: translateX(-50%);
    li {
      width: 7px;
      height: 7px;
      background-color: #e6e3e2;
      border-radius: 50%;
      margin: 2.5px;
      cursor: pointer;
      transition: .2s ease;
      &.slick-active {
        background-color: #f26536;
      }
    }
    button {
      display: none;
    }
  }
}

.qoute {
  position: relative;
  padding-bottom: 70px;
  &>img {
    display: none;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  &__bg {
    position: absolute;
    top: -42px;
    left: 50%;
    transform: translateX(-50%);
  }
  &__inner {
    position: relative;
    max-width: 515px;
    margin-left: auto;
    padding-top: 80px;
    margin-right: 39px;
    & > p {
      color: #000000;
      font-family: "BerlingskeSlab-Md";
      font-size: 18px;
      font-weight: 400;
      line-height: 28px;
      letter-spacing: 0.36px;
      margin-bottom: 35px;
    }
  }
  &__by {
    display: flex;
    justify-content: space-between;
    color: #474747;
    font-family: "BerlingskeSlab-MdItalic";
    font-size: 16px;
    font-weight: 400;
    line-height: 30px;
    letter-spacing: 0.32px;
    p:last-of-type {
      margin-top: 14px;
    }
    & > span {
      width: 119px;
      position: relative;
      top: -43px;
      left: -14px;
    }
    span {
      display: block;
    }
  }


}

@media screen and (max-width: 1240px) {
  .team {
    &__feature {
      padding: 40px 20px 20px 20px;
      p {
        br {
          display: none;
        }
      }
    }
    &__member:nth-of-type(1) {
      .team__member-info {
        left: 47px;
      }
      img {
        left: 0;
      }
    }
    &__wrapper {
      margin-top: 83px;
    }
  }
}

@media screen and (max-width: 1140px) {
  .team {
    &__wrapper {
      max-width: 100%;
    }
    .slick-list {
      overflow: visible;
      width: 100%;
      max-width: 100%;
    }
    &__member {
      width: 203px;
    }
  }
}

@media screen and (max-width: 992px) {
  .team {
    margin-top: 0;
  }
  .qoute {
    padding-bottom: 400px;
    margin-bottom: 40px;
    & > img:first-of-type {
      display: block;
    }
    &__mobile-arrow {
      display: block!important;
      left: 37%!important;
      bottom: 37%!important;
    }
    &:before {
      content: "";
      width: 1227px;
      position: absolute;
      bottom: 25%;
      left: -32px;
      height: 196px;
      background-image: linear-gradient(180deg,#fff,hsla(0,0%,100%,.0));
    }
    &:after {
      content: "";
      position: absolute;
      width: 1227px;
      height: 46%;
      background-image: url(../img/qoute_mobile_bg.png);
      background-size: 1266px;
      background-position: bottom left;
      bottom: 0;
      left: -221px;
      z-index: -1;
    }
    &__inner {
      max-width: 100%;
    }
    &__bg {
      display: none;
    }
  }
}


@media screen and (max-width: 768px) {
  .qoute {
    &__inner {
      & > p {
        font-size: 16px;
        line-height: 25px;
        margin-bottom: 15px;
      }
    }
    &__mobile-arrow {
      left: 30%!important;
    }
  }
  .team__header {
    h2 {
      font-size: 32px;
      line-height: 44px;
      span:after {
        width: 110%;
        height: 120%;
      }
    }
  }
  .team {
    &__features {
      flex-wrap: wrap;
    }
    &__feature {
      max-width: 420px;
      flex-basis: 100%;
      min-height: 142px;
      &:last-of-type:after {
        right: -22px;
      }
      &:nth-of-type(n) {
        margin: 0 auto 60px;
      }
    }
    &__wrapper {
      &:before {
        display: none;
      }
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 600px) {
  .qoute {
    &:after {
      height: 371px;
    }
    &:before {
      height: 202px;
    }
    &__mobile-arrow {
      bottom: 40%!important;
      left: 60px!important;
    }

    &__inner {
      margin-right: 0;
      & > p {
        font-size: 12px;
        line-height: 20px;
        margin-bottom: 6px;
      }
    }
    &__by {
      margin-top: 30px;
      font-size: 12px;
      line-height: 20px;
      position: relative;
      align-items: flex-end;
      & > span {
        position: absolute;
        left: auto;
        top: -50px;
        right: -15px;
        img {
          width: 78px;
          height: 67px;
        }
      }
    }
  }
  .team {
    h2 {
      margin-top: 80px;
      font-size: 20px;
      line-height: 30px;
      span {
        margin-top: 5px;
        &:after {
          width: 105%;
          height: 130%;
        }
      }
    }
    &__feature {
      max-width: 300px;
      padding-left: 40px;
      &:last-of-type:after {
        display: none;
      }
      p {
        font-size: 13px;
        line-height: 22px;
        br {
          display: block;
        }
      }
    }
    &__member-info {
      span {
        &:first-of-type {
          font-size: 12px;
          line-height: 15px;
        }
        &:nth-of-type(2) {
          font-size: 10px;
          line-height: 15px;
          margin-top: 12px;
        }
      }
    }
    &__member {
      &:nth-of-type(1) {
        .team__member-info {
          span {
            &:first-of-type {
              &:after {
                width: 93px;
                height: 36px;
              }
            }
            &:last-of-type {
              &:after {
