              }
            }
            &:last-of-type {
              &:after {
                left: -26px;
                top: -11px;
              }
            }
          }
        }
      }
      &:nth-of-type(3) {
        .team__member-info {
          span {
            &:first-of-type {
              &:after {
                width: 93px;
                height: 36px;
                left: -31px;
                top: -12px;
            
              }
            }
            &:last-of-type {
              &:after {
                left: -18px;
                top: -9px;
              }
            }
          }
        }
      }
      &:nth-of-type(4) {
        .team__member-info {
          span {
            &:first-of-type {
              &:after {
                width: 93px;
                height: 36px;
                left: -26px;
                top: -12px;
            
              }
            }
            &:last-of-type {
              &:after {
                left: -18px;
                top: -9px;
              }
            }
          }
        }
      }
      &:nth-of-type(5) {
        .team__member-info {
          span {
            &:first-of-type {
              &:after {
                width: 93px;
                height: 36px;
                left: -26px;
                top: -12px;
            
              }
            }
            &:last-of-type {
              &:after {
                left: -18px;
                top: -9px;
              }
            }
          }
        }
      }
      &:nth-of-type(6) {
        .team__member-info {
          span {
            &:first-of-type {
              &:after {
                width: 93px;
                height: 36px;
                left: -34px;
                top: -12px;
            
              }
            }
            &:last-of-type {
              &:after {
                left: -26px;
                top: -11px;
              }
            }
          }
        }
      }
    }
  }
}