.header {
  padding-top: 23px;
  padding-bottom: 23px;
  position: relative;
  z-index: 10;
  &__wrapper {
    display: flex;
    align-items: center;
    & > p {
      max-width: 170px;
      color: #282828;
      font-family: 'BerlingskeSlab-Md';
      font-weight: normal;
      font-size: 14px;
      line-height: 21px;
    }
  }

  &__logo {
    margin-right: 30px;
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-left: auto;
    padding-top: 6px;
    padding-bottom: 20px;
  }

  &__phone {
    color: #2d2d2d;
    font-family: 'BerlingskeSlab-XBd';
    font-size: 22px;
    font-weight: 400;
    line-height: 21px;
    letter-spacing: 0.44px;
    margin-bottom: 9px;
    border-bottom: 1px solid transparent;
    transition: .2s ease;
    &:hover {
      border-bottom-color: #000;
    }
  }

  &__status {
    color: #696969;
    font-family: "BerlingskeSlab-DBd";
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    letter-spacing: 0.28px;
    position: relative;
    margin-bottom: 12px;
    &:after {
      content: "";
      width: 5px;
      height: 5px;
      background-color: #2abf1b;
      border-radius: 50%;
      left: -10px;
      top: 50%;
      display: block;
      position: absolute;
      transform: translateY(-50%);
    }
  }
  &__callback {
    color: #32a7ff;
    font-family: "BerlingskeSlab-Md";
    font-size: 15px;
    font-weight: 400;
    line-height: 21px;
    letter-spacing: 0.3px;
    border-bottom: 1px dotted #32a7ff;
    transition: .2s ease;
    &:hover {
      border-bottom-color: transparent;
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    &__logo {
      margin-right: 15px;
    }
  }
}

@media screen and (max-width: 600px) {
  .header {
    &__wrapper {
      flex-direction: column;
      align-items: center;
      & > p {
        font-size: 12px;
        max-width: 180px;
      }
    }
    &__logo {
      margin: 0;
    }
    &__contacts {
      margin-left: 0;
      align-items: center;
      margin-top: 44px;
    }
    &__phone {
      margin-bottom: 7px;
    }
    &__status {
      margin-bottom: 4px;
    }
  }
}