html {
    touch-action: manipulation;
}

body {
    position: relative;
    margin: 0;
    padding: 0;
    font-family: 'BerlingskeSlab', sans-serif;
    font-size: 14px;
    line-height: 24px;
    // overflow-x: hidden;
}

section {
    // overflow-x: hidden;
}

.container {
    max-width: 1200px;
    width: 100%;
    margin: auto;
    padding: 0 15px;

    @media screen and (max-width: 600px) {
        padding: 0 10px;
    }
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.apple-fix {
    width: 100%;
    position: fixed;
}

.no-scroll {
    overflow: hidden;
}

.img-wrapper {
    display: inline-block;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.clearfix {
    &::after {
        content: ".";
        display: block;
        clear: both;
        visibility: hidden;
        line-height: 0;
        height: 0;
    }
}

.wrapper {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    width: 100%;
    overflow-x: hidden;

    .header {
        flex: 0 0 auto;
    }

    main {
        position: relative;
        flex: 1 0 auto;
    }

    .footer {
        flex: 0 0 auto;
    }
}

input,
textarea {
    font-family: inherit;
    font-size: 16px;
    line-height: 25px;
    color: #222;
    outline: none;
    border-radius: 0;
    border: none;
    background-color: rgba(0, 0, 0, 0);
    width: 100%;
    transition: border-color .2s linear;
    padding: 0;
}

label.error {
    display: none !important;
}

button {
    cursor: pointer;
}

@keyframes spinner {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(720deg);
    }
}

button[disabled] {
    cursor: not-allowed;
}