* {
    box-sizing: border-box;
    outline: none;
}

*::before,
*::after {
    box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
    padding: 0;
}

ul,
ol {
    margin: 0;

    &.no-style {
        padding: 0;
        list-style: none;
    }
}

button {
    outline: none;
    border: none;
}

img {
    border: none;
}

a {
    text-decoration: none;
    color: inherit;
}

*:focus {
    outline: none;
}

[contenteditable=true]:empty:before {
    content: attr(data-placeholder);
    color: #131313;
    font-size: inherit;
    display: block;
    /* For Firefox */
}

select {
    appearance: none;

    &::-ms-expand {
        //  IE
        display: none;
    }
}

input[type='number'] {
    -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

h1 {

    @media screen and (max-width: 768px) {}
}

h2 {

    @media screen and (max-width: 768px) {}
}

h3 {

    @media screen and (max-width: 768px) {}
}

