.offer {
  position: relative;
  z-index: 2;
  .container {
    padding-top: 120px;
    position: relative;
    & > img {
      position: absolute;
      z-index: -1;
      top: 121px;
      right: 31px;
    }
  }
  &__main {
    width: 631px;
    height: 750px;
    position: relative;
    padding: 88px 71px 0;
    margin-left: auto;
  }
  &__bg {
    position: absolute;
    top: -68px;
    left: -83px;
    z-index: -1;
  }
  h3 {
    color: #2a2a2a;
    font-size: 30px;
    font-weight: 400;
    line-height: 40px;
    font-family: "BerlingskeSlab-DBdItalic";
    letter-spacing: 0.6px;
    text-align: center;
    span {
      display: inline;
      font-family: "BerlingskeSlab-XBd";
    }
  }
  &__cta {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #535353;
    font-family: "BerlingskeSlab-Md";
    font-size: 18px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0.36px;
    margin-bottom: 45px;
    img {
      margin-right: 15px;
    }
  }
  &__point-text {
    padding-left: 30px;
    color: #535353;
    font-size: 18px;
    font-weight: 400;
    font-family: "BerlingskeSlab-Md";
    letter-spacing: 0.36px;
    line-height: 28px;
    span {
      &:first-of-type {
        font-family: "BerlingskeSlab-DBdItalic";
        position: relative;
        display: block;
        margin-bottom: 3px;
        color: #444444;
        &:before {
          content: "";
          width: 8px;
          height: 8px;
          background-image: url(../img/step_marker.svg);
          background-repeat: no-repeat;
          background-size: 8px;
          position: absolute;
          left: -25px;
          top: 9px;
        }
      }
    }
  }

  .btn {
    height: 95px;
    width: 100%;
    margin: 55px 0 31px;
    span {
      font-size: 22px;
    }
  }

  &__note {
    color: #3a3a3a;
    font-family: "BerlingskeSlab-Lt";
    font-size: 14px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0.28px;
    text-align: center;
  }

  .file_upload {
    margin-top: 100px;
  }
}

@media screen and (max-width: 992px) {
  .offer {
    &__main {
      width: 570px;
      padding: 88px 40px 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .offer {
    h3 {
      font-size: 24px;
      line-height: 33px;
    }
    &__cta {
      margin-bottom: 30px;
    }
    &__point-text {
      font-size: 16px;
      line-height: 24px;
    }
    .btn {
      max-width: 360px;
      width: 100%;
      text-align: center;
      margin: 30px auto;
      padding: 0 20px;
      span {
        font-size: 16px;
      }
    }
    .container {
      padding-top: 0;
      &>img {
        width: 468px;
        height: 247px;
        bottom: -135px;
        top: auto;
        left: 50%;
        right: auto;
        transform: translateX(-50%);
      }
    }
    &:after {
      content: "";
      background-image: url(../img/recommendations_mobile_bg.png);
      position: absolute;
      width: 394px;
      height: 100%;
      top: 100px;
      right: -150px;
      background-repeat: no-repeat;
      z-index: -1;
    }
    &__bg {
      display: none;
    }
    &__main {
      width: 100%;
      padding: 80px 15px 0;
    }
    .file_upload {
      margin: 0;
      bottom: 17px;
      right: 20px;
    }
  }
}

@media screen and (max-width: 600px) {
  .offer {
    &__main {
      padding: 50px 0 163px;
      height: auto;
    }
    h3 {
      font-size: 18px;
      line-height: 30px;
      margin-bottom: 10px;
    }
    &__cta {
      font-size: 12px;
      img {
        margin-right: 10px;
      }
    }
    &__point-text {
      font-size: 12px;
      line-height: 20px;
    }
    .btn {
      height: 80px;
      margin: 25px auto;
      span {
        font-size: 15px;
      }
    }
  }
}