.work-model {
  margin-bottom: 100px;
  h2 {
    margin-bottom: 74px;
    color: #2a2a2a;
    font-family: "BerlingskeSlab-XBd";
    font-size: 40px;
    font-weight: 400;
    line-height: 65px;
    letter-spacing: 0.8px;
    text-align: center;
    span {
      color: #ffffff;
      font-family: "BerlingskeSlab-DBdItalic";
      position: relative;
      display: block;
      width: fit-content;
      margin: auto;
      &:after {
        content: "";
        width: 744px;
        height: 74px;
        background-color: #009ee0;
        background-image: linear-gradient(0deg,#40adff,#78c5ff);
        position: absolute;
        transform: translate(-50%,-50%) skewX(-8deg);
        left: 50%;
        top: 50%;
        border-radius: 3px;
        z-index: -1;
      }
    }
  }
  &__list {
    display: flex;
    justify-content: space-between;
  }
  &__item {
    flex-basis: 25%;
    padding-top: 205px;
    position: relative;
    &:nth-of-type(1) {
      .work-model__img {
        left: 58px;
      }
    }
    &:nth-of-type(2) {
      .work-model__img {
        left: -70px;
        top: -18px;
      }
    }
    &:nth-of-type(3) {
      .work-model__img {
        left: 37px;
        top: 18px;
      }
    }
    &:nth-of-type(4) {
      .work-model__img {
        left: 85px;
        top: 21px;
      }
    }
  }
  &__img {
    position: absolute;
    top: 0;
    left: 0;
  }
  p {
    color: #000000;
    font-family: "BerlingskeSlab-Md";
    font-size: 18px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0.36px;
    text-align: center;
  }
}

@media screen and (max-width: 992px) {
  .work-model {
    &__item {
      padding-top: 170px;
      padding-left: 10px;
      padding-right: 10px;

      &:nth-of-type(1) {
        .work-model__img {
          left: 30px;
        }
        img {
          width: 90%;
        }
      }
      &:nth-of-type(3) {
        .work-model__img {
          left: 30px;
        }
      }
      &:nth-of-type(4) {
        .work-model__img {
          left: 72px;
          top: 27%;
          transform: translateY(-50%);
        }
      }
    }
    p {
      br {
        display: none;
      }
    }
    &__img {
      img {
        width: 90%;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .work-model {
    h2 {
      font-size: 32px;
      line-height: 50px;
      span:after {
        width: 110%;
        height: 107%;
      }
    }
    &__list {
      flex-wrap: wrap;
    }
    &__item {
      margin-bottom: 30px;
      flex-basis: 50%;
      &:nth-of-type(n) {
        .work-model__img {
          left: 50%;
          transform: translateX(-50%);
          top: 0;
        }
      }
      &:nth-of-type(2) {
        .work-model__img {
          left: 82px;
          transform: translateX(-29%);
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .work-model {
    margin-bottom: 50px;
    h2 {
      font-size: 20px;
      line-height: 30px;
      margin-bottom: 50px;
      max-width: 251px;
      margin: 0 auto 50px;
      span {
        margin-top: 5px;
      }
    }
    &__list {
      flex-wrap: wrap;
    }
    &__item {
      margin-bottom: 0;
      flex-basis: 50%;
      padding-top: 125px;
      &:nth-of-type(1) {
        .work-model__img {
          width: 95px;
          height: 108px;
          img {
            width: 100%;
          }
        }
      }
      &:nth-of-type(2) {
        .work-model__img {
          width: 197px;
          height: 165px;
          left: 50%;
          transform: translateX(-50%);
          top: 0px;
        }
      }
      &:nth-of-type(3) {
        .work-model__img {
          width: 129px;
          height: 106px;
          left: 50%;
          transform: translateX(-50%);
          top: 14px;
        }
      }
      &:nth-of-type(4) {
        .work-model__img {
          width: 90px;
          height: 113px;
          top: 28px;
          left: 59%;
        }
      }
    }
    p {
      font-size: 12px;
      line-height: 20px;
    }
    &__img {
      img {
        width: 100%;
      }
    }
  }
}