.consult {
  padding: 96px 0 113px;
  position: relative;
  background-image: url(../img/consult_bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  // margin-bottom: 85px;
  & > p {
    position: absolute;
    left: -78px;
    transform: rotate(-90deg) translateY(-50%);
    opacity: .11;
    color: #353535;
    font-family: Berlingske Slab\ - Md;
    font-size: 148px;
    font-weight: 400;
    line-height: 100%;
    letter-spacing: 2.96px;
    top: 38%;
  }
  .container {
    position: relative;
    z-index: 1;
  }
  h2 {
    color: #2a2a2a;
    font-family: "BerlingskeSlab-XBd";
    font-size: 40px;
    font-weight: 400;
    letter-spacing: 0.8px;
    line-height: 50px;
    margin-bottom: 52px;
    span {
      display: block;
      position: relative;
      color: #ffffff;
      font-family: "BerlingskeSlab-DBdItalic";
      line-height: 70px;
      width: fit-content;
      margin-left: 20px;
      margin-top: 10px;
      &:after {
        content: "";
        width: 430px;
        height: 74px;
        background-color: #009ee0;
        background-image: linear-gradient(0deg,#40adff,#78c5ff);
        position: absolute;
        transform: translate(-50%,-50%) skewX(-8deg);
        left: 50%;
        top: 50%;
        border-radius: 3px;
        z-index: -1;
      }
    }
  }
  &__info {
    color: #353535;
    font-size: 22px;
    font-weight: 400;
    line-height: 32px;
    font-family: "BerlingskeSlab-XBd";
    letter-spacing: 0.44px;
    letter-spacing: 0.44px;
    position: relative;
    max-width: 430px;
    margin-left: 15px;
    margin-bottom: 67px;
    span {
      font-family: "BerlingskeSlab-Md";
    }
  }
  .btn {
    width: 480px;
    height: 95px;
    span {
      font-size: 19px;
      margin-left: 10px;
    }
  }
  &__mobile-decor, &__mobile-decor-man {
    display: none;
  }
}

@media screen and (max-width: 1366px) {
  .consult {
    background-position: center;
    & > p {
      display: none;
    }
  }
}

@media screen and (max-width: 992px) {
  .consult {
    background-image: url(../img/consult_mobile_bg.png);
    background-size: 1920px 100%;
    background-position: 1%;
    &__mobile-decor {
      display: block;
      position: absolute;
      bottom: -114px;
      right: -35px;
    }
    &__mobile-decor-man {
      display: block;
      position: absolute;
      right: 4%;
      bottom: -114px;
    }
    .btn {
      max-width: 360px;
      width: 100%;
      text-align: center;
      img {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .consult {
    padding-bottom: 0;
    position: relative;
    &:after {
      content: "";
      width: 100%;
      height: 4px;
      background-image: url(../img/hero_border.png);
      background-repeat: repeat;
      position: absolute;
      bottom: -4px;
      left: 0;
      z-index: -1;
    }
    .container {
      padding-bottom: 420px;
    }
    &__mobile-decor-man {
      width: 230px;
      left: 50%;
      right: auto;
      transform: translateX(-50%);
      bottom: 0;
    }
    &__mobile-decor {
      bottom: 0;
      right: -165px;
    }
    h2 {
      text-align: center;
      font-size: 32px;
      line-height: 40px;
      margin-bottom: 30px;
      span {
        margin: auto;
        &:after {
          width: 120%;
          height: 85%;
        }
      }
    }
    &__info {
      font-size: 16px;
      line-height: 24px;
      text-align: center;
      margin: auto;
      max-width: 430px;
      margin-bottom: 25px;
    }
    .arrow-left {
      &:after {
        top: 8px;
      }
    }
    .btn {
      margin: 0 auto;
      max-width: 337px;
      width: 100%;
      text-align: center;
      height: 76px;
    }
  }
}

@media screen and (max-width: 600px) {
  .consult {
    padding-top: 60px;
    h2 {
      font-size: 20px;
      line-height: 30px;
      margin-bottom: 20px;
      span {
        line-height: 57px;
        &::after{
          width: 117%;
          height: 83%;
        }
      }
    }
    &__info {
      font-size: 12px;
      line-height: 20px;
      max-width: 260px;
    }
    .arrow-left:after {
      top: 6px;
      left: -13px;
    }
    .btn {
      padding: 0 55px;
      height: 80px;
      max-width: 320px;
      width: 100%;
      span {
        font-size: 15px;
        line-height: 22px;
      }
    }
    &__mobile-decor-man {
      width: 270px;
    }
  }
}