.response {
  display: flex;
  align-items: flex-end;
  align-items: center;
  
  &__msg {
    max-width: 595px;
    text-align: left;
    margin-left: auto;
  }

  &__decor {
    margin-right: 40px;
    img {
      display: block;
      width: 100%;
    }
  }

  h3 {
    color: #2a2a2a;
    font-family: "BerlingskeSlab-DBdItalic";
    font-size: 30px;
    font-weight: 400;
    line-height: 40px;
    letter-spacing: 0.6px;
    margin-bottom: 30px;
    span {
      font-family: "BerlingskeSlab-XBd";
    }
  }
  p {
    color: #353535;
    font-family: "BerlingskeSlab-Md";
    font-size: 22px;
    font-weight: 400;
    line-height: 32px;
    letter-spacing: 0.44px;
  }
}

@media screen and (max-width: 992px) {
  .response {
    h3 {
      font-size: 24px;
      line-height: 32px;
      margin-bottom: 20px;
    }
    p {
      font-size: 16px;
      line-height: 24px;
    }
  }
}

@media screen and (max-width: 768px) {
  .response {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    &__msg {
      max-width: 100%;
    }
    &__decor {
      max-width: 350px;
    }
  }
}

@media screen and (max-width: 600px) {
  .response {
    h3 {
      font-size: 20px;
      line-height: 30px;
      margin-bottom: 15px;
    }
    p {
      font-size: 12px;
      line-height: 20px;
    }
  }
}