.hero {
  height: 100vh;
  margin-top: -158px;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  position: relative;
  padding-top: 25vh;
  &:after {
    content: "";
    width: 100%;
    height: 4px;
    background-image: url(../img/hero_border.png);
    background-repeat: repeat;
    position: absolute;
    bottom: -4px;
    left: 0;
  }

  h1 {
    color: #2a2a2a;
    font-family: "BerlingskeSlab-XBd";
    font-size: 50px;
    font-weight: 400;
    letter-spacing: 1px;
    line-height: 65px;
    text-align: center;
    max-width: 840px;
    margin: auto;
  }

  h2 {
    color: #ffffff;
    font-family: "BerlingskeSlab-XBdItalic";
    font-size: 45px;
    letter-spacing: 0.9px;
    line-height: 70px;
    position: relative;
    text-align: center;
    margin-bottom: 40px;

    span {
      position: relative;
      z-index: 2;
    }
    
    &:after {
      content: "";
      background-color: #009ee0;
      background-image: linear-gradient(0deg,#40adff 0,#78c5ff);
      position: absolute;
      width: 615px;
      height: 74px;
      transform: translate(-50%,-50%) skewX(-15deg);
      left: 50%;
      top: 50%;
      border-radius: 6px;
      z-index: 1;
    }
  }

  &__info {
    text-align: center;
    margin: auto;
    max-width: 440px;
    color: #353535;
    font-family: "BerlingskeSlab-Md";
    font-size: 22px;
    font-weight: 400;
    line-height: 32px;
    letter-spacing: 0.44px;
    margin-bottom: 50px;
    span {
      font-family: "BerlingskeSlab-XBd";
      letter-spacing: 0.44px;
      position: relative;
    }
  }

  &__cta {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 2;
    img {
      position: absolute;
      left: 65px;
      top: 39%;
      transform: translateY(-50%);
      // display: none;
    }

    p {
      color: #3a3a3a;
      font-family: "BerlingskeSlab-Lt";
      font-size: 15px;
      font-weight: 400;
      line-height: 25px;
      letter-spacing: 0.3px;
      max-width: 375px;
      text-align: center;
      a {
        color: #32a7ff;
        font-family: "BerlingskeSlab-LtItalic";
        letter-spacing: 0.3px;
        transition: .2s ease;
        text-decoration: underline;
        text-decoration-color: transparent;
        &:hover {
          text-decoration-color: currentColor;
        }
      }
    }
  }

  .btn {
    width: 404px;
    height: 95px;
    margin-bottom: 30px;
    span {
      font-size: 22px;
    }
  }
}

@media screen and (max-width: 1140px) {
  .hero {
    &__cta {
      img {
        left: 0px;
        top: 33%;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .hero {
    height: auto;
    padding-top: 250px;
    padding-bottom: 40px;

    h1 {
      font-size: 36px;
      line-height: 130%;
    }

    h2 {
      font-size: 28px;
      &:after {
        width: 400px;
        height: 59px;
      }
    }
    &__info {
      margin-bottom: 77px;
    }
  }
}

@media screen and (max-width: 768px) {
  .hero {
    &__cta {
      img {
        left: -22%;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .hero {
    margin-top: -340px;
    padding-top: 345px;

    .container {
      padding: 0 9px;
    }

    h1 {
      font-size: 18px;
      line-height: 130%;
      font-weight: 700;
      font-style: bold;
    }

    h2 {
      font-size: 18px;
      line-height: 36px;
      margin-bottom: 27px;
      &:after {
        width: 246px;
        height: 33px;
      }
    }
    &__info {
      font-size: 12px;
      line-height: 20px;
      max-width: 250px;
    }
    .arrow-left:after {
      top: 3px;
    }
    .btn {
      max-width: 420px;
      width: 100%;
      height: 71px;
      margin-bottom: 25px;
      span {
        font-size: 15px;
      }
    }
    &__cta {
      img {
        left: -196px;
        top: 10%;
      }
      p {
        position: relative;
        z-index: 1;
        font-size: 12px;
        line-height: 20px;
      }
    }
  }
}