.hover_info {
  &__icon {
    height: 47px;
    width: 47px;
    background-color: #40adff;
    background-image: linear-gradient(to top, #40adff 0%, #78c5ff 100%);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
  }
  i {
    height: 20px;
  }
}