.catalog { 
  margin-top: 120px;
  margin-bottom: 50px;
  // overflow: hidden;


  &__info {
    position: relative;
    margin-bottom: 25px;
    &:after {
      content: "";
      width: 204px;
      transform: translateX(-50%);
      left: 75%;
      top: -50px;
      height: 204px;
      position: absolute;
      background-image: url(../img/coat_arms_russia_PNG11.png);
      background-size: 204px;
    }
  }

  h2 {
    color: #2a2a2a;
    font-family: "BerlingskeSlab-XBd";
    font-size: 40px;
    font-weight: 400;
    line-height: 65px;
    letter-spacing: 0.8px;
    margin-bottom: 14px;
    text-align: center;
  }

  &__text-info {
    display: flex;
    justify-content: center;
    align-items: center;
    p {
      margin-left: 15px;
      color: #535353;
      font-family: "BerlingskeSlab-Md";
      font-size: 18px;
      font-weight: 400;
      line-height: 28px;
      letter-spacing: 0.36px;
    }
  }

  &__tabs {
    ul {
      display: flex;
      justify-content: center;
      align-items: center;
      list-style-type: none;
      border-radius: 10px;
      overflow: hidden;
      max-width: 472px;
      margin: 0 auto 50px;
      padding: 0;

      li {
        width: 236px;
        height: 83px;
        background-color: #f3f3f3;
        color: #6c6c6c;
        font-family: "BerlingskeSlab-Md";
        font-size: 15px;
        font-weight: 400;
        line-height: 26px;
        letter-spacing: 0.3px;
        transition: .2s ease;
        position: relative;

        &:after {
          content: '';
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          bottom: 0;
          right: 0;
          background-image: linear-gradient(to top, #f35e36 0%, #f37636 100%);
          opacity: 0;
          transition: .2s ease;
        }

        a {
          width: 100%;
          height: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          position: relative;
          z-index: 2;
        }

        &.ui-tabs-active {
          color: #fff;
          &::after {
            opacity: 1;
          }
        }
      }
    }
  }

  &__grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
  }

  &__grid-item {
    display: flex!important;
    flex-wrap: wrap;
    justify-content: flex-start;
    width: 100%;
  }

  &__item {
    flex-basis: 23.07%;
    max-width: 23.07%;
    width: 100%;
    margin-right: 1.92%;
    position: relative;
    // height: 300px;
    margin-bottom: 37px;
    box-shadow: 0px 0px 55px rgba(0,0,0,0.11);
    padding: 7px;
    border-radius: 5px;

    img {
      display: block;
      width: 100%;
      max-height: 193px;
      margin-bottom: 40px;
    }

    span {
      color: #353535;
      font-family: "BerlingskeSlab-DBd";
      font-size: 17px;
      font-weight: 400;
      line-height: 23px;
      letter-spacing: 0.34px;
      text-align: center;
      height: 36px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 17px;
    }

    &:nth-of-type(4n) {
      margin-right: 0;
    }

    &:hover {
      .catalog__item-hover {
        opacity: 1;
        visibility: visible;
        transform: scale(1);
      }
    }
  }

  &__item-hover {
    position: absolute;
    width: 100%;
    min-height: 100%;
    padding: 18px;
    background-color: #fff;
    top: 0;
    left: 0;
    box-shadow: 0 23px 140px 11px rgba(93, 93, 93, 0.14);
    border-radius: 5px;
    opacity: 0;
    transform: scale(0.8);
    transform-origin: center;
    visibility: hidden;
    transition: .2s linear;
    z-index: 5;
    display: flex;
    flex-direction: column;
    p {
      color: #353535;
      font-family: "BerlingskeSlab-Md";
      font-size: 14px;
      font-weight: 400;
      line-height: 23px;
      letter-spacing: 0.28px;
      margin-bottom: 23px;
    }
    .btn {
      width: 100%;
      height: 61px;
      margin-bottom: 25px;
      margin-top: auto;
      span {
        color: #fff;
        margin-bottom: 0;
      }
    }
  }

  .slick-list {
    max-width: 100%;
    overflow: visible;
    position: relative;
    z-index: 2;
  }

  .slick-dots {
    li {
      width: 7px;
      height: 7px;
      background-color: #e6e3e2;
      border-radius: 50%;
      margin: 2.5px;
      cursor: pointer;
      transition: .2s ease;
      &.slick-active {
        background-color: #f26536;
      }
    }
    button {
      display: none;
    }
  }

  // &__bg {
  //   position: absolute;
  //   width: 100%;
  //   height: 300px;
  //   clip-path: polygon(100% 0, 100% 0, 100% 100%, 0 100%, 0 78%, 13% 71%, 0 65%, 0 0);
  //   background-color: #fff;
  //   &:after {
  //     content: '';
  //     width: 100%;
  //     height: 100%;
  //     // filter: drop-shadow(0px 0px 50px rgba(0,0,0,0.16));
  //     box-shadow: 0px 0px 50px rgba(0,0,0,0.16);
  //     top: 0;
  //     left: 0;
  //     position: absolute;
  //   }
  // }
}

@media screen and (max-width: 992px) {
  .catalog {
    padding-top: 80px;
    position: relative;
    z-index: 2;
    &__item {
      flex-basis: 24%;
      max-width: 24%;
      margin-right: 1%;
      &:nth-of-type(4n+4) {
        margin-right: 0%;
      }
      img {
        margin-bottom: 7%;
      }
      span {
        margin-bottom: 5%;
        font-size: 14px;
        line-height: 17px;
      }
    }
    &__item-hover {
      height: auto;
      padding: 9px;
      min-height: 100%;
      .btn {
        height: 40px;
        margin-bottom: 10px;
        span {
          margin-bottom: 0;
        }
      }
      p {
        font-size: 12px;
        line-height: 18px;
        margin-bottom: 10px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .catalog {
    &__tabs {
      ul  {
        margin-bottom: 30px;
        li {
          width: 50%;
          height: 67px;
        }
      }
    }
    h2 {
      font-size: 32px;
      line-height: 55px;
    }
    &__text-info {
      p {
        font-size: 15px;
      }
    }
    &__item {
      flex-basis: 49%;
      max-width: 49%;
      margin-right: 2%;
      span {
        // font-size: 12px;
        margin-bottom: 3%;
      }
      img {
        margin-bottom: 5%;
        max-height: 100%;
      }
      &:nth-of-type(4n+4) {
        margin-right: 2%;
      }
      &:nth-of-type(2n+2) {
        margin-right: 0;
      }
    }
    &__item-hover {
      height: auto;
      padding: 15px;
      .btn {
        height: 60px;
        max-width: 360px;
        width: 100%;
        margin: 0 auto 15px;
        span {
          margin-bottom: 0;
        }
      }
      p {
        font-size: 14px;
        line-height: 22px;
        margin-bottom: 15px;
      }
    }
    &__grid-item {
      margin: 0 15px;
    }
  }
}

@media screen and (max-width: 600px) {
  .catalog {
    margin-top: -50px;
    padding-bottom: 65px;
    margin-bottom: 0;
    &__grid-item {
      margin: 0 10px;
    }
    &__info:after {
      left: 100%;
    }
    &__text-info {
      p {
        font-size: 12px;
        line-height: 20px;
      }
    }
    h2 {
      font-size: 20px;
      line-height: 35px;
    }
    &__item {
      span {font-size: 12px;}
    }
    &__item-hover {
      padding: 8px;
      &>span {
        height: auto;
      }
      p {
        font-size: 10px;
        line-height: 16px;
      }
      .btn {
        height: 35px;
      }
    }
  }
}