.file_upload {
  width: 138px;
  height: 138px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  pointer-events: none;
  & > img {
    width: 138px;
    height: 138px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    z-index: 0;
  }
  p {
    text-align: center;
    position: relative;
    z-index: 1;
    color: #626262;
    font-family: "BerlingskeSlab-MdItalic";
    font-size: 14px;
    font-weight: 400;
    line-height: 26px;
    letter-spacing: 0.28px;
    img {
      display: block;
      margin: 0 auto 5px;

    }
    span {
      &:first-of-type {
        font-family: "BerlingskeSlab-DBdItalic";
      }
    }
  }
}