.expert {
  margin-top: 100px;
  .container {
    position: relative;
    padding: 145px 82px 45px 108px;
  }

  &__bg {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
  }

  &__decor {
    position: absolute;
    left: -6%;
    top: -65px;
    z-index: 2;
    display: block;
    pointer-events: none;
  }

  &__wrapper {
    position: relative;
    z-index: 5;
  }

  &__header {
    color: #2a2a2a;
    font-family: "BerlingskeSlab-XBd";
    font-size: 40px;
    font-weight: 400;
    letter-spacing: 0.8px;
    line-height: 65px;
    max-width: 520px;
    text-align: center;
    margin-bottom: 46px;
    span {
      color: #ffffff;
      font-family: "BerlingskeSlab-DBdItalic";
      line-height: 70px;
      position: relative;
      z-index: 2;
      display: block;
      &:after {
        content: '';
        width: 513px;
        height: 74px;
        background-color: #009ee0;
        background-image: linear-gradient(to top, #40adff 0%, #78c5ff 100%);
        position: absolute;
        transform: translate(-50%,-50%) skewX(-15deg);
        left: 50%;
        top: 50%;
        border-radius: 6px;
        z-index: -1;
      }
    }
  }

  &__about {
    color: #353535;
    font-family: "BerlingskeSlab-XBd";
    font-size: 22px;
    font-weight: 400;
    line-height: 32px;
    letter-spacing: 0.44px;
    max-width: 520px;
    padding: 0 80px 0 100px;
    p {
      font-family: "BerlingskeSlab-Md";
      display: inline;
    }
    span {
      position: relative;
    }
  }

  &__info {
    max-width: 395px;
    width: 100%;
    margin-left: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    span {
      font-family: "BerlingskeSlab-DBdItalic";
      font-size: 18px;
      font-weight: 400;
      line-height: 23px;
      letter-spacing: 0.36px;
      position: relative;

      &:first-of-type {
        color: #fff;
        z-index: 2;
        &:after {
          content: '';
          width: 130px;
          height: 54px;
          background-color: #009ee0;
          background-image: linear-gradient(to top, #40adff 0%, #78c5ff 100%);
          position: absolute;
          transform: translate(-50%,-50%) skewX(-2deg);
          left: 50%;
          top: 50%;
          border-radius: 2px;
          z-index: -1;
        }
      }
      &:nth-of-type(2) {
        margin-top: 20px;
        margin-left: 30px;
        &:after {
          content: '';
          width: 394px;
          height: 54px;
          background-color: #ffffff;
          position: absolute;
          transform: translate(-50%,-50%) skewX(-4deg);
          left: 50%;
          top: 50%;
          border-radius: 2px;
          z-index: -1;
        }
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .expert {
    &__bg {
      left: 42%;
    }

    .container {
      padding: 100px 84px 130px 57px;
    }

    &__decor {
      width: 135px;
      left: -3%;
    }

    .arrow-left:after {
      top: 7px;
    }
    &__about {
      font-size: 18px;
      line-height: 24px;
      max-width: 350px;
      padding: 0;
  
    }

    &__header {
      font-size: 28px;
      line-height: 40px;
      text-align: left;
      margin-bottom: 25px;
      span {
        width: fit-content;
        &:after {
          width: 354px;
          height: 52px;
        }
      }
    }
    &__info {
      margin-top: 60px;
    }
  }
}

@media screen and (max-width: 600px) {
  .expert {
    background-image: url(../img/expert_decor_mobile.png);
    background-position: center;
    &__decor {
      width: 100px;
    }
    &__bg {
      display: none;
    }
    .container {
      padding-top: 50px;
      padding-left: 10px;
      padding-right: 10px;
      padding-bottom: 45px;
    }
    h2 {
      text-align: center;
      font-size: 20px;
      line-height: 36px;
      margin-bottom: 25px;
      span {
        margin: auto;
        line-height: 38px;
        &:after {
          width: 270px;
          height: 45px;
        }
      }
    }
    &__about {
      font-size: 12px;
      max-width: 260px;
      margin: auto;
      line-height: 20px;
      margin-bottom: 250px;
    }
    &__info {
      margin-top: 0;
      margin-bottom: 60px;
      margin-left: 25px;
      span {
        font-size: 12px;
        &:first-of-type {
          &:after {
            width: 90px;
            height: 38px
          }
        }
        &:last-of-type {
          margin-top: 10px;
          margin-left: 10px;
          &:after {
            width: 273px;
            height: 38px;
          }
        }
      }
    }
  }
}