.accept {
  background-image: url(../img/accept_bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: left top;
  margin-bottom: 83px;
  .container {
    padding: 123px 20px 140px;
    position: relative;
    z-index: 1;
  }
  h2 {
    color: #2a2a2a;
    font-family: "BerlingskeSlab-XBd";
    font-size: 40px;
    font-weight: 400;
    letter-spacing: 0.8px;
    line-height: 65px;

  }
  h3 {
    color: #ffffff;
    font-family: "BerlingskeSlab-DBdItalic";
    letter-spacing: 0.8px;
    line-height: 70px;
    font-size: 40px;
    position: relative;
    width: fit-content;
    margin-left: 20px;
    margin-bottom: 60px;
    &:after {
      content: "";
      width: 562px;
      height: 74px;
      background-color: #009ee0;
      background-image: linear-gradient(0deg,#40adff,#78c5ff);
      position: absolute;
      transform: translate(-50%,-50%) skewX(-6deg);
      left: 50%;
      top: 50%;
      border-radius: 3px;
      z-index: -1;
    }
  }
  h4 {
    color: #353535;
    font-family: "BerlingskeSlab-Md";
    font-size: 22px;
    font-weight: 400;
    line-height: 32px;
    letter-spacing: 0.44px;
    margin-bottom: 80px;
  }
  &__ico {
    width: 315px;
    margin-left: 200px;
    display: flex;
    img {
      width: 47px;
      height: 47px;
      position: relative;
      top: -10px;
      margin-right: 16px;
    }
    span {
      color: #353535;
      font-family: "BerlingskeSlab-XBd";
      font-size: 22px;
      font-weight: 400;
      line-height: 32px;
      letter-spacing: 0.44px;
    }
  }
}

@media screen and (max-width: 1366px) {
  .accept {
    background-position: center;
  }
}

@media screen and (max-width: 1140px) {
  .accept {
    background-position: 44% 50%;
  }
}

@media screen and (max-width: 992px) {
  .accept {
    h2 {
      text-align: center;
    }
    h3 {
      margin: 0 auto 30px;
    }
    h4 {
      text-align: center;
    }
    &__ico {
      margin: auto;
      img {
        top: 0;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .accept {
    h2,h3 {
      font-size: 32px;
      line-height: 44px;
    }
    h3:after {
      width: 115%;
      height: 106%;
    }
    h4 {
      font-size: 16px;
      margin-bottom: 30px;
    }
    &__ico {
      span {
        font-size: 17px;
        line-height: 120%;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .accept {
    margin-bottom: 60px;
    .container {
      padding-top: 60px;
      padding-left: 9px;
      padding-right: 9px;
    }
    h2,h3 {
      font-size: 19px;
      line-height: 30px;
    }
    h3 {
      margin-top: 5px;
      margin-bottom: 20px;
      &:after {
        height: 115%;
      }
    }
    h4 {
      font-size: 12px;
      line-height: 120%;
      margin-bottom: 30px;
    }
    &__ico {
      max-width: 315px;
      width: 100%;
      span {
        font-size: 15px;
        line-height: 150%;
      }
    }
  }
}