.comments {
  padding: 95px 0 112px;
  background-color: #f7f7f7;
  position: relative;
  .container {
    position: relative;
    z-index: 1;
  }
  h2 {
    text-align: center;
    font-size: 40px;
    font-weight: 400;
    color: #2a2a2a;
    font-family: "BerlingskeSlab-XBd";
    letter-spacing: 0.8px;
    line-height: 70px;
    margin-bottom: 35px;
    span {
      width: fit-content;
      margin: auto;
      text-align: center;
      position: relative;
      color: #ffffff;
      font-family: "BerlingskeSlab-DBdItalic";
      display: block;
      &:after {
        content: "";
        width: 644px;
        height: 74px;
        background-color: #009ee0;
        background-image: linear-gradient(0deg,#40adff,#78c5ff);
        position: absolute;
        transform: translate(-50%,-50%) skewX(-8deg);
        left: 50%;
        top: 50%;
        border-radius: 3px;
        z-index: -1;
      }
    }
  }
  &__tip {
    max-width: 370px;
    margin: 0 auto 48px;
    color: #535353;
    font-family: "BerlingskeSlab-Md";
    font-size: 18px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0.36px;
    text-align: center;
  }
  &__list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
  }
  &__item {
    flex-basis: 32.1%;
    margin-right: 1.19%;
    margin-bottom: 40px;
    img {
      display: block;
      width: 100%;
    }
    &:nth-of-type(3n+3) {
      margin-right: 0;
    }
  }

  .slick-dots {
    list-style-type: none;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    padding: 0;
    margin-top: 15px;
    li {
      width: 7px;
      height: 7px;
      background-color: #e6e3e2;
      border-radius: 50%;
      margin: 2.5px;
      cursor: pointer;
      transition: .2s ease;
      &.slick-active {
        background-color: #f26536;
      }
    }
    button {
      display: none;
    }
  }
}

@media screen and (max-width: 768px) {
  .comments {
    h2 {
      font-size: 32px;
      line-height: 59px;
      span {
        &:after {
          height: 106%;
          width: 110%;
        }
      }
    }
    &__item {
      flex-basis: 49%;
      margin-right: 2%;
      margin-bottom: 0;
      &:nth-of-type(3n+3) {
        margin-right: 2%;
      }
      &:nth-of-type(2n+2) {
        margin-right: 0;
      }
    }
  }
}

@media screen and (max-widtH: 600px) {
  .comments {
    padding-top: 60px;
    padding-bottom: 60px;
    h2 {
      font-size: 20px;
      line-height: 30px;
      margin-bottom: 30px;
      span {
        margin-top: 6px;
        white-space: nowrap;
        &:after {
          width: 105%;
          height: 137%;
        }
      }
    }
    &__tip {
      font-size: 12px;
      line-height: 20px;
      margin-bottom: 18px;
    }
    &__item {
      &:nth-of-type(n) {
        margin-right: 7px;
      }
    }
  }
}