.footer {
  padding-top: 150px;
  background-image: url(../img/footer_bg.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: left top;
  overflow: hidden;

  &__map {
    height: 553px;
    box-shadow: 0 24px 61px 12px rgba(106, 106, 106, 0.15);
    background-color: #d3d3d3;
    margin-bottom: 70px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: relative;
  }
  &__map-info {
    width: 460px;
    height: 465px;
    box-shadow: 0 23px 140px 11px rgba(93, 93, 93, 0.06);
    border-radius: 4px;
    background-color: #f1f1f1;
    position: relative;
    margin-right: 40px;
  }
  &__map-inner {
    position: absolute;
    top: 7px;
    left: 7px;
    right: 7px;
    bottom: 7px;
    width: calc(100% - 14px);
    height: calc(100% - 14px);
    border-radius: 4px;
    background-color: #ffffff;
    padding: 77px 0 0 79px;
  }
  &__map-item {
    margin-bottom: 50px;
    &:last-of-type {
      margin-bottom: 0;
    }
    span {
      text-transform: uppercase;
      color: #919191;
      font-family: "Berlingske Slab - D Bd";
      font-size: 16px;
      letter-spacing: 0.32px;
      display: block;
    }
    a {
      color: #282828;
      font-family: "BerlingskeSlab-Md";
      font-size: 18px;
      font-weight: 400;
      line-height: 34px;
      letter-spacing: 0.44px;
      transition: .2s linear;
      text-decoration: underline;
      text-decoration-color: transparent;
      &:hover {
        text-decoration-color: currentColor;
      }
    }
  }
  &__inner {
    display: flex;
    align-items: center;
  }
  &__logo {
    margin-right: 30px;
  }
  &__info {
    margin-right: auto;
    span, a {
      display: block;
      font-size: 14px;
      font-weight: 400;
      line-height: 21px;
      font-family: "BerlingskeSlab-Md";
    }

    span {
      color: #282828;
    }

    a {
      color: #32a7ff;
      text-decoration: underline;
      transition: .2s linear;
      &:hover {
        text-decoration-color: transparent;
      }
    }
  }
  &__deployed {
    font-family: "BerlingskeSlab-DBd";
    font-size: 16px;
    font-weight: 400;
    line-height: 27px;
    color: #32a7ff;
    font-size: 18px;
    margin-right: 150px;

    span {
      color: #282828;
      display: block;
    }
  }
}

#map {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;

}

@media screen and (max-width: 992px) {
  .footer {
    &__deployed {
      margin: 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    background-image: url(../img/footer_mobile_bg.png);
    background-size: 722px 403px;
    background-position: bottom left;
    .container {
      padding: 0;
    }
    &__map {
      box-shadow: none;
      height: auto;
      background: none;
      flex-direction: column;
      margin-bottom: 30px;
    }
    &__map-info {
      width: 100%;
      height: auto;
      margin: 0;
    }
    &__map-inner {
      position: relative;
      top: auto;
      left: auto;
      right: auto;
      bottom: auto;
      margin: 0;
      width: 100%;
      height: auto;
      padding: 70px 40px;
    }
    &__map-item {
      a {
        br {
          display: none;
        }
      }
    }
    #map {
      height: 285px;
      position: relative;
    }
    &__inner {
      flex-direction: column;
    }
    &__logo {
      margin: 0 0 30px 0;
    }
    &__info {
      text-align: center;
      margin: 0 auto 35px;
    }
    &__deployed {
      font-size: 16px;
      line-height: 26px;
      margin-bottom: 40px;
    }
  }
}