.catalog-download {
  padding: 90px 0 100px 45px;
  background-image: url(../img/catalog_download_bg.png);
  background-size: 110%;
  background-position: center center;
  background-repeat: no-repeat;
  margin-top: 150px;
  position: relative;
  &:after {
    content: "";
    width: 100%;
    height: 4px;
    background-image: url(../img/hero_border.png);
    background-repeat: repeat;
    position: absolute;
    bottom: -4px;
    left: 0;
    z-index: -1;
    opacity: 0;
  }

  .container {
    position: relative;
  }

  h2 {
    max-width: 375px;
    color: #2a2a2a;
    font-size: 30px;
    font-weight: 400;
    line-height: 40px;
    font-family: "BerlingskeSlab-DBdItalic";
    letter-spacing: 0.6px;
    margin-bottom: 34px;
    span {
      font-family: "BerlingskeSlab-XBd";
    }
  }

  .btn {
    width: 219px;
    height: 61px;
    span {
      font-size: 16px;
    }
  }

  &__decor {
    position: absolute;
    right: 50px;
    top: 50%;
    transform: translateY(-50%);
  }

  &__arrow {
    bottom: -32px;
    left: 23%;
    position: absolute;
  }

  .file_upload {
    position: absolute;
    top: -110px;
    left: 50%;
  }
}

@media screen and (max-width: 1240px) {
  .catalog-download {
    background-size: 1920px;
    &__decor {
      width: 630px;
    }
    &__arrow {
      left: 26%;
    }
  }
}

@media screen and (max-width: 1140px) {
  .catalog-download {
    &__arrow {
      display: none;
    }
    &__decor {
      width: 500px;
    }
    .file_upload {
      left: 53%;
    }
  }
}

@media screen and (max-width: 992px) {
  .catalog-download {
    background-image: url(../img/catalog_download_mobile.png);
    margin-top: 75px;
    background-repeat: repeat;
    padding-bottom: 150px;
    &:after {
      opacity: 1;
    }
    .btn {
      // margin-bottom: 200px;
    }
    &__decor {
      top: 100%;
      width: 350px;
    }
    .file_upload {
      top: auto;

    }
  }
}

@media screen and (max-width: 768px) {
  .catalog-download {
    .file_upload {
      bottom: -180px;
      left: -35px;
    }
    &__decor {
      width: 350px;
      left: 0;
      top: 140%;
    }
  }
}

@media screen and (max-width: 600px) {
  .catalog-download {
    padding-top: 55px;
    margin-top: -15px;
    padding-left: 12px;
    h2 {
      font-size: 20px;
      line-height: 30px;
    }
    .file_upload {
      left: -13px;
    }
    &__decor {
      top: 151%;
    }
    .btn {
      max-width: 360px;
      width: 100%;
      span {
        font-size: 15px;
      }
    }
  }
}