.default-modal {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  background-color: rgba(#f5f5f5, 1);
  transition: all 0.3s ease-in-out;
  opacity: 0;
  visibility: hidden;
  z-index: -1;
  -webkit-overflow-scrolling: touch;

  &.active {
    opacity: 1;
    visibility: visible;
    z-index: 1001;

    .default-modal__content {
      transform: scale(1);
      opacity: 1;
      // transform: translateY(0);
    }

    .default-modal__wrapper {
      opacity: 1;
      transform: translateY(0);
    }
  }

  &__close {
    width: 33px;
    height: 33px;
    position: absolute;
    right: -50px;
    top: -50px;
    cursor: pointer;
    z-index: 2;

    @media screen and (max-width: 1140px) {
      right: 10px;
    }
  }

  &__content {
    max-width: 970px;
    height: 660px;
    padding: 0 20px;
    position: relative;
    width: 100%;
    margin: auto;
    opacity: 0;
    transform: scale(0.9);
    transform-origin: center;
    transition: all 0.3s ease-in-out;
    z-index: 1;

    &:before {
      content: '';
      display: block;
      width: 90%;
      height: 90%;
      position: absolute;
      z-index: -5;
      top: 5%;
      left: 5%;
      box-shadow: 0 23px 203px 15px rgba(93, 93, 93, .08);
    }
  }

  &__form {
    max-width: 378px;
    margin-left: auto;
    padding-top: 65px;
    form {
      margin-top: 30px;
    }
    h2 {
      text-align: center;
      color: #2a2a2a;
      font-size: 30px;
      font-weight: 400;
      line-height: 40px;
      font-family: "BerlingskeSlab-DBdItalic";

      span {
        font-family: "BerlingskeSlab-XBd";
        display: block;
      }
    }

    h3 {
      color: #535353;
      font-family: "BerlingskeSlab-Md";
      font-size: 18px;
      font-weight: 400;
      line-height: 28px;
      letter-spacing: 0.36px;
      text-align: center;
      max-width: 260px;
      margin: 19px auto 0;
    }

    .default-input:first-of-type {
      margin-top: 30px;
    }

    .default-input {
      margin-left: 0;
      margin-right: 0;
      width: 100%;
      max-width: 100%;
    }

    .btn {
      height: 95px;
      width: 100%;
      margin-bottom: 25px;

      span {
        color: #fff;
        font-size: 19px;
      }
    }

    p {
      text-align: center;
      color: #3a3a3a;
      font-family: "BerlingskeSlab-Lt";
      font-size: 14px;
      font-weight: 400;
      line-height: 22px;
      letter-spacing: 0.28px;
      max-width: 260px;
      margin: auto;

      a {
        color: #32a7ff;
        font-family: "BerlingskeSlab-LtItalic";
        text-decoration: underline;
      }
    }
  }
}

.callback {
  .default-modal {
    &__content {
      padding: 0;
    }

    &__inner {
      background-image: url(../img/callback_bg.png);
      height: 100%;
      width: 100%;
      padding-right: 75px;
    }
  }

  @media screen and (max-width: 890px) {
    .default-modal {
      &__inner {
        background-image: none;
        padding: 0 20px;
      }

      &__content {
        background-color: #fff;
        width: 95%;
        border-radius: 5px;

        &:before {
          display: none;
        }
      }

      &__form {
        max-width: 420px;
        margin: auto;
      }
    }
  }

  @media screen and (max-width: 600px) {
    .default-modal {
      &__content {
        height: 520px;
      }

      &__form {
        form {
          margin-top: 15px;
        }
        h2 {
          font-size: 20px;
          line-height: 20px;
        }

        h3 {
          font-size: 12px;
          line-height: 20px;
          margin-top: 10px;
        }

        .default-input:first-of-type {
          margin-top: 15px;
        }

        .default-input {
          margin-bottom: 15px;
        }

        .btn {
          height: 75px;

          span {
            font-size: 14px;
          }
        }
      }
    }
  }

  @media screen and (max-height: 800px) {
    overflow: auto;
    .default-modal__content {
      margin-top: 160px;
      margin-bottom: 40px;
    } 
  }
}

