.radio-container {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  position: relative;
  cursor: pointer;
  user-select: none;
  justify-content: flex-end;
  margin-bottom: 15px;

  @media screen and (max-width: 600px) {
     font-size: 12px;
  }

  input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;

    &:checked ~ .checkmark {
      background-color: #009ee0;
    }
    
    &:checked ~.checkmark:after {
      display: block;
    }
  }

  .checkmark {
    position: relative;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    min-width: 25px;
    background-color: #eee;
    border-radius: 50%;
    margin-right: 10px;
    transition: .2s ease;
    &:after {
      content: "";
      position: absolute;
      display: none;
      top: 9px;
      left: 9px;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background: white;
    }
  }

  &:hover input ~ .checkmark {
    background-color: #ccc;
  }
}