.attention {
  margin-top: 200px;
  margin-bottom: 90px;

  h2 {
    color: #2a2a2a;
    font-family: "BerlingskeSlab-XBd";
    font-size: 40px;
    font-weight: 400;
    line-height: 65px;
    letter-spacing: 0.8px;
    margin-bottom: 50px;
    text-align: center;
    span {
      font-family: "BerlingskeSlab-DBdItalic";
      line-height: 70px;
      color: #ffffff;
      position: relative;
      display: block;
      margin: auto;
      width: fit-content;
      &:after {
        content: "";
        width: 798px;
        height: 74px;
        background-color: #009ee0;
        background-image: linear-gradient(0deg,#40adff 0,#78c5ff);
        position: absolute;
        transform: translate(-50%,-50%) skewX(-6deg);
        left: 50%;
        top: 50%;
        border-radius: 3px;
        z-index: -1;
      }
    }
  }

  &__list {
    display: flex;
    justify-content: space-between;
  }

  &__item {
    display: flex;
    flex-direction: column;
    text-align: center;
    flex-basis: 25%;
    & >
    span {
      background-image: linear-gradient(to top, #f35e36 0%, #f37636 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      color: #000000;
      font-family: "BerlingskeSlab-Lt";
      font-size: 92px;
      font-weight: 400;
      line-height: 100%;
      margin-bottom: 30px;
      letter-spacing: 1.74px;
      position: relative;
      &:after {
        content: '';
        width: 192px;
        height: 106px;
        left: 39%;
        position: absolute;
        background-image: url(../img/attention_decor.png);
        background-repeat: no-repeat;
        background-position: left;
        pointer-events: none;
      }
    }
  }

  &__text {
    color: #000000;
    font-family: "BerlingskeSlab-Md";
    font-size: 18px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0.36px;

    i {
      position: relative;
      margin: 8px 0 0 22px;
      position: relative;
      top: 4px;
      width: 36px;
      height: 36px;
      img {
        width: 5px;
        height: 12px;
      }
      &:after {
        content: '';
        width: 36px;
        height: 36px;
        background-color: #f35e36;
        background-image: linear-gradient(to top, #f35e36 0%, #f37636 100%);
        border-radius: 50%;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        z-index: -1;
      }

    }
  }
  &__hover-trigger {
    position: absolute;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 5;
    &:hover {
      .attention__hover-content {
        transform: translateY(-50%) scale(1);
        opacity: 1;
        visibility: visible;
      }
    }
  }
  &__hover-content{ 
    position: absolute;
    box-shadow: 0 23px 82px 3px rgba(93, 93, 93, 0.08);
    border-radius: 10px;
    background-color: #ffffff;
    padding: 60px 60px 60px 50px;
    text-align: left;
    width: 478px;
    z-index: 5;
    left: 30px;
    top: 50%;
    transform: translateY(-50%) scale(0.8);
    opacity: 0;
    visibility: hidden;
    transform-origin: center;
    transition: .2s linear;
    &:after {
      content: '';
      position: absolute;
      pointer-events: none;
      width: calc(100% - 16px);
      height: calc(100% - 16px);
      top: 8px;
      left: 8px;
      right: 8px;
      bottom: 8px;
      border-radius: 10px;
      border: 1px solid #f3f3f3;
    }
    h4 {
      color: #353535;
      font-family: "BerlingskeSlab-XBd";
      font-size: 20px;
      font-weight: 400;
      line-height: 23px;
      letter-spacing: 0.4px;
      font-style: normal;
      margin-bottom: 30px;
    }
    ul {
      list-style-type: none;
      padding: 0;
    }
    li {
      display: flex;
      margin-bottom: 25px;
      &:last-of-type {
        margin-bottom: 0;
      }
    }
    span {
      color: #ffffff;
      font-family: "BerlingskeSlab-DBdItalic";
      font-size: 13px;
      font-weight: 400;
      line-height: 17.1px;
      letter-spacing: 0.43px;
      width: 32px;
      height: 32px;
      min-width: 32px;
      background-color: #ececec;
      background-image: linear-gradient(to top, #f35e36 0%, #f37636 100%);
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 18px;
      &:after {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 1140px) {
  .attention {
    &__text {
      padding: 0 10px;
      br {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .attention {
    h2 {
      span {
        max-width: 555px;
        line-height: 41px;
        &:after {
          width: 550px;
          height: 100px;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .attention {
    h2 {
      font-size: 32px;
      line-height: 40px;
      span:after {
        width: 490px;
        height: 85px;
      }
    }
    &__list {
      flex-wrap: wrap;
    }
    &__item {
      flex-basis: 50%;
      margin-bottom: 40px;
    }
    &__text {
      padding: 0 20px;
    }
    &__hover-content {
      width: 400px;
      padding: 40px 30px 40px 30px;
      left: 50%;
      transform: translateX(-50%) translateY(-50%) scale(0.8);
    }
    &__hover-trigger {
      &:hover {
        .attention__hover-content {
          transform: translateX(-50%) translateY(-50%) scale(1);
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .attention {
    margin-top: 125px;
    margin-bottom: 40px;
    h2 {
      font-size: 20px;
      span {
        max-width: 225px;
        line-height: 27px;
      }
      span:after {
        width: 275px;
        height: 61px;
      }
    }
    &__text {
      padding: 0 10px;
      font-size: 12px;
      line-height: 20px;
      position: relative;

      i:after {
        width: 28px;
        height: 28px;
      }
    }
    &__hover-trigger {
      width: 28px;
      height: 28px;
    }
    &__item {
      & > span {
        margin-bottom: 25px;
        &:after {
          left: 24%;
        }
      }
    }
    &__hover-content {
      width: 320px;
      padding: 30px 20px;
      left: 37px;

      p {
        font-size: 12px;
        line-height: 20px;
      }
    }
  }
}

@media screen and (max-width: 420px) {
  .attention {
    &__text {
      i {
        position: absolute;
        top: -23px;
        right: 5px;
      }
    }
  }
}