.steps {
  margin-top: 90px;

  h2 {
    color: #2a2a2a;
    font-family: "BerlingskeSlab-XBd";
    font-size: 40px;
    font-weight: 400;
    letter-spacing: 0.8px;
    line-height: 65px;
    text-align: center;
    margin: 0 auto 100px;

    span {
      position: relative;
      z-index: 2;
      display: block;
      color: #ffffff;
      font-family: "BerlingskeSlab-DBdItalic";
      line-height: 70px;
      &:after {
        content: "";
        background-color: #009ee0;
        background-image: linear-gradient(0deg,#40adff 0,#78c5ff);
        position: absolute;
        width: 731px;
        height: 74px;
        transform: translate(-50%,-50%) skewX(-15deg);
        left: 50%;
        top: 50%;
        border-radius: 6px;
        z-index: -1;
      }
    }
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &__item {
    flex-basis: 48.5%;
    position: relative;
    height: 268px;
    margin-bottom: 70px;
    &:after {
      content: '';
      position: absolute;
      width: 80%;
      height: 70%;
      left: 50%;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
      box-shadow: 0 0 120px rgba(0,0,0,0.12);
      z-index: -1;
    }
    & > svg {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      top: -145px;
      width: 780px;
      height: 606px;
      &:nth-of-type(2) {
        display: none;
      }
    }

    &-wrapper {
      position: relative;
      z-index: 2;
      height: 100%;
      overflow: hidden;
      padding-top: 45px;
    }
  }

  &__item-num {
    position: absolute;
    width: 52px;
    height: 52px;
    background-color: #ececec;
    background-image: linear-gradient(to top, #f35e36 0%, #f37636 100%);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-family: "BerlingskeSlab-DBdItalic";
    font-size: 18px;
    font-weight: 400;
    letter-spacing: 0.36px;
    top: -31px;
    left: 50%;
    transform: translateX(-48%);
  }

  &__step-num {
    opacity: .64;
    color: #f1f1f1;
    font-family: BerlingskeSlab-XBd;
    font-size: 229px;
    bottom: 19%;
    left: -9%;
    z-index: -1;
    position: absolute;
  }

  &__item-header {
    padding-left: 35px;
    color: #353535;
    font-family: BerlingskeSlab-XBd;
    font-size: 20px;
    font-weight: 400;
    line-height: 23px;
    letter-spacing: .4px;
    position: relative;
    height: 45px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: fit-content;
    &:after {
      content: ""; 
      width: 105%; 
      transform: skew(-5deg); 
      height: 45px; 
      background-color: #eee; 
      left: 3px; 
      top: 0px; 
      display: block; 
      position: absolute; 
      z-index: -1;
      border-radius: 3px;
    }
  }

  &__item-result {
    padding-left: 73px;
    margin-top: 16px;
    padding-right: 15px;
    position: relative;
    font-size: 18px;
    font-weight: 400;
    color: #444444;
    font-family: "BerlingskeSlab-DBdItalic";
    line-height: 28px;
    letter-spacing: 0.36px;
    &:before {
      content: '';
      width: 8px;
      height: 8px;
      background-image: url(../img/step_marker.svg);
      background-repeat: no-repeat;
      background-size: 8px;
      position: absolute;
      left: 50px;
      top: 9px;
    }
    span {
      font-family: "BerlingskeSlab-Md";
      color: #535353;
      display: block;
    }
  }
}

@media screen and (max-width: 1200px) {
  .steps {
    &__item {
      max-width: 100%;
      flex-basis: 100%;
      &:after {
        width: 40%;
      }
      &-wrapper {
        max-width: 568px;
        margin: auto;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .steps {
    margin-top: 70px;

    h2 {
      font-size: 32px;
      line-height: 36px;
      margin: 0 auto 55px;
      span {
        &:after {
          width: 560px;
          height: 60px;
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .steps {
    h2 {
      font-size: 19px;
      line-height: 39px;
      span {
        line-height:25px;
        width: fit-content;
        margin: auto;
        padding: 0 10px;
        &:after {
          width: 100%;
          height: 130%;
        }
      }
    }
  }
  .steps__item {
    & > svg {
      width: 502px;
      &:nth-of-type(1) {
        display: none;
      }
      &:nth-of-type(2) {
        display: block;
      }
    }
    &-wrapper {
      max-width: 290px;
      padding-top: 60px;
    }
    &-header {
      font-size: 14px;
      line-height: 20px;
      padding-left: 20px;
      padding-right: 33px;
      height: auto;
      &:after {
        left: -3px;
        top: 50%;
        width: 91%;
        height: 138%;
        min-height: 48px;
        transform: translateY(-50%) skew(-5deg);
      }
    }
    &-result {
      font-size: 12px;
      line-height: 20px;
      margin-top: 22px;
      padding-left: 52px;
      &:before {
        left: 34px;
        top: 5px;
      }
    }
    &:last-of-type {
      margin-bottom: 45px;
    }
  }
}