.recommendations {
  margin-top: 90px;
  .container {
    position: relative;
  }

  &__bg {
    position: absolute;
    top: -65px;
    left: -65px;
    z-index: -1;
  }

  &__wrapper {
    padding: 80px 100px;
  }

  h2 {
    text-align: center;
    color: #2a2a2a;
    font-family: "BerlingskeSlab-XBd";
    font-size: 40px;
    font-weight: 400;
    letter-spacing: 0.8px;
    line-height: 65px;
    line-height: 70px;
    margin-bottom: 20px;
    span {
      font-family: "BerlingskeSlab-DBdItalic";
    }
  }

  &__info {
    position: relative;
    text-align: center;
    color: #353535;
    font-family: "BerlingskeSlab-Md";
    font-size: 22px;
    font-weight: 400;
    line-height: 32px;
    letter-spacing: 0.44px;
    max-width: 580px;
    margin: auto;
    span {
      font-family: "BerlingskeSlab-XBd";
    }
  }

  &__inputs {
    display: flex;
    justify-content: space-between;
    margin-top: 35px;
    margin-bottom: 10px;
  }

  .default-input {
    flex-basis: 31%;
    margin-right: 2%;
    margin-left: 0;
    &:last-of-type {
      margin-right: 0;
    }
  }

  .btn {
    width: 437px;
    height: 95px;
    margin: auto;
    span {
      font-size: 22px;
      font-family: "BerlingskeSlab-XBd";
    }
  }
}

@media screen and (max-width: 1240px) {
  .recommendations {
    &__bg {
      left: 55%;
      transform: translateX(-50%);
    }
  }
}

@media screen and (max-width: 1140px) {
  .recommendations {
    &__wrapper {
      padding: 80px 30px;
    }
  }
}

@media screen and (max-width: 992px) {
  .recommendations {
    &__bg {
      top: -20px;
    }
  }
}

@media screen and (max-width: 768px) {
  .recommendations {
    position: relative;
    &:after {
      content: '';
      background-image: url(../img/recommendations_mobile_bg.png);
      position: absolute;
      width: 394px;
      height: 100%;
      top: 100px;
      right: -150px;
      background-repeat: no-repeat;
      z-index: -1;
    }
    &__bg {
      display: none;
    }
    &__wrapper {
      padding: 0;
    }
    h2 {
      font-size: 32px;
      line-height: 48px;
    }
    .arrow-left:after {
      left: -13px;
      top: 8px;
    }
    &__info {
      max-width: 430px;
      font-size: 16px;
      line-height: 26px;
    }
    &__inputs {
      flex-direction: column;
      align-items: center;
    }
    .default-input {
      margin-left: 0;
      margin-right: 0;
    }
  }
}

@media screen and (max-width: 600px) {
  .recommendations {
    h2 {
      font-size: 20px;
      max-width: 180px;
      margin: 0 auto 20px;
      line-height: 32px;
    }
    .arrow-left {
      &:after {
        left: 14px;
        top: 6px;
      }
    }
    &__info {
      font-size: 12px;
      line-height: 20px;
      width: 100%;
      max-width: 300px;
      margin-bottom: 23px;
      br {
        display: none;
      }
    }
    &__inputs {
      margin-top: 23px;
    }
    .default-input {
      margin-bottom: 15px;
    }
    .btn {
      height: 80px;
      max-width: 360px;
      width: 100%;
      span {
        font-size: 15px;
      }
    }
  }
}