.repair {
  max-width: 813px;
  width: 100%;
  height: 249px;
  box-shadow: 0 23px 82px 3px rgba(93, 93, 93, 0.08);
  border-radius: 10px;
  background-color: #ffffff;
  margin: 0 auto;
  position: relative;
  padding: 8px;
  &:after {
    content: '';
    border-radius: 10px;
    border: 1px solid #f3f3f3;
    position: absolute;
    top: 8px;
    left: 8px;
    bottom: 8px;
    right: 8px;
    width: calc(100% - 16px);
    height: calc(100% - 16px);
    pointer-events: none;
  }
  &__decor-top {
    position: absolute;
    width: 96%;
    height: 97%;
    z-index: -1;
    box-shadow: 0 23px 140px 11px rgba(93,93,93,.06);
    background-color: #fff;
    transform: skew(-13deg);
    top: 16px;
    left: 41px;
    border-radius: 10px;
  }
  &__decor-bottom {
    position: absolute;
    height: 66%;
    width: 74%;
    top: 39px;
    left: 236px;
    transform: skew(-27deg);
    z-index: -5;
    box-shadow: 0 23px 140px 11px rgba(93, 93, 93, 0.06);
    background-color: #ffffff;
    border-radius: 10px;
  }
  &__inner {
    overflow: hidden;
    border-radius: 10px;
    height: 100%;
    padding-right: 107px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  &__img {
    position: absolute;
    z-index: 0;
    left: 0;
    top: 7px;
    img {
      position: absolute;
      left: 0;
    }
  }
  &__before {
    top: 0;
  }
  &__after {
    display: none;
    top: -7px;
  }
  &__text {
    position: relative;
    z-index: 1;
    color: #2a2a2a;
    font-family: "BerlingskeSlab-DBdItalic";
    font-size: 30px;
    font-weight: 400;
    line-height: 40px;
    letter-spacing: 0.6px;
    max-width: 390px;
    span {
      font-family: "BerlingskeSlab-XBd";
    }
  }

  &__btn {
    position: absolute;
    width: 47px;
    height: 47px;
    background-color: #40adff;
    background-image: linear-gradient(to top, #40adff 0%, #78c5ff 100%);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 15%;
    top: 25%;
    img {
      animation-fill-mode: forwards;
      animation-name: spinner;
      animation-duration: 4s;
      animation-timing-function: linear;
      animation-play-state: paused;
      animation-iteration-count: infinite;
    }
    &:hover {
      img {
        animation-play-state: running;
      }
    }
  } 
}

@media screen and (max-width: 768px) {
  .repair {
    &__inner {
      padding-right: 20px;
    }
    &__text {
      max-width: 315px;
      font-size: 24px;
      line-height: 32px;
    }
  }
}

@media screen and (max-width: 600px) {
  .repair {
    height: 398px;
    &__inner {
      align-items: flex-start;
      justify-content: center;
      padding: 40px 0 0 0;
    }
    &__img {
      top: auto;
      bottom: 0;
    }
    &__before {
      top: auto;
      bottom: 0px;
    }
    &__after {
      top: auto;
      bottom: 0px;
    }
    &__btn {
      bottom: 35%;
      top: auto;
      left: 60%;
    }
    &__text {
      font-size: 20px;
      font-weight: 400;
      line-height: 30px;
      text-align: center;
    }
  }
}