.q-block {
  background-image: url(../img/q_block_decor.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  overflow: hidden;
  .container {
    position: relative;
    padding: 84px 0 100px;
  }
  h2 {
    color: #2a2a2a;
    font-family: "BerlingskeSlab-XBd";
    font-size: 40px;
    font-weight: 400;
    line-height: 65px;
    letter-spacing: 0.8px;
    text-align: center;
    margin-bottom: 30px;
    padding: 0 10px;
  }
  h3 {
    color: #353535;
    font-family: "BerlingskeSlab-Md";
    font-size: 22px;
    font-weight: 400;
    line-height: 32px;
    letter-spacing: 0.44px;
    text-align: center;
    max-width: 590px;
    margin: 0 auto 70px;
    padding: 0 10px;
  }
  &__main-img {
    position: absolute;
    bottom: 0;
    left: -100px;
  }
  &__wrapper {
    display: flex;
  }
  &__form {
    width: 100%;
    max-width: 630px;
    height: 389px;
    // margin-left: auto;
    position: relative;
    &-bg {
      position: absolute;
      width: 899px;
      height: 807px;
      top: -66px;
      left: -77px;
    }
  }
  &__question {
    position: relative;
    padding: 35px 54px 20px;
    height: 412px;
    p {
      color: #2a2a2a;
      font-size: 24px;
      font-weight: 400;
      line-height: 36px;
      font-family: "BerlingskeSlab-DBdItalic";
      letter-spacing: 0.6px;
      text-align: center;
      margin-bottom: 15px;
      span {
        font-family: "BerlingskeSlab-XBd";
        display: block;
      }
    }
  }
  &__status {
    color: #535353;
    font-family: "BerlingskeSlab-Md";
    font-size: 18px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0.36px;
    text-align: center;
    display: block;
    margin-bottom: 20px;
  }
  &__answers {
    display: flex;
    // justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    label {
      flex-basis: 50%;
    }
  }
  &__next {
    width: 137px;
    height: 65px;
    border-radius: 10px;
    background-image: linear-gradient(to top, #f35e36 0%, #f37636 100%);
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-family: "BerlingskeSlab-Lt";
    font-size: 15px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0.3px;
    margin: -50px 70px 17px auto;
    transition: .2s linear;
    position: absolute;
    right: 0;
    bottom: 22px;
    &:hover {
      background-color: darken(#f3f3f3, 6);
    }
  }
  &__main-info {
    position: relative;
    z-index: 1;
    margin-left: auto;
    margin-right: 70px;
    margin-top: auto;
    top: 20px;
    span {
      color: #ffffff;
      font-family: "BerlingskeSlab-DBdItalic";
      font-size: 18px;
      font-weight: 400;
      line-height: 23px;
      letter-spacing: 0.36px;
      position: relative;
      z-index: 1;
      &:after {
        content: "";
        width: 141px;
        height: 54px;
        background-color: #009ee0;
        background-image: linear-gradient(0deg,#40adff 0,#78c5ff);
        position: absolute;
        transform: translate(-50%,-50%) skewX(-2deg);
        left: 50%;
        top: 50%;
        border-radius: 2px;
        z-index: -1;
      }
    }
    p {
      font-family: BerlingskeSlab-DBdItalic;
      font-size: 18px;
      font-weight: 400;
      line-height: 23px;
      letter-spacing: .36px;
      position: relative;
      margin-left: 35px;
      margin-top: 20px;
      z-index: 0;
      &:after {
        content: "";
        width: 198px;
        height: 54px;
        background-color: #fff;
        position: absolute;
        transform: translate(-50%,-50%) skewX(-4deg);
        left: 50%;
        top: 50%;
        border-radius: 2px;
        z-index: -1;
      }
    }
  }
  .btn {
    max-width: 360px;
    width: 100%;
    height: 70px;
    margin: auto;
    font-size: 18px;
  }
  .default-input {
    margin-bottom: 13px;
    padding: 21px 34px;
  }
}

@media screen and (max-width: 1140px) {
  .q-block {
    h3 {
      margin: 0 auto 122px;
    }
    &__main-img {
      left: -161px;
    }
  }
}

@media screen and (max-width: 992px) {
  .q-block {
    &__main-img {
      left: 50%;
      transform: translateX(-50%);
    }
    h3 {
      margin: 0 auto 50px;
    }
    &__wrapper {
      flex-direction: column;
    }
    &__main-info {
      order: 1;
      margin: auto;
      top: -105px;
      left: -49px;
    }
    &__form {
      margin: 0 auto 500px;
    }
  }
}

@media screen and (max-width: 768px) {
  .q-block {
    h2 {
      font-size: 32px;
      line-height: 44px;
      margin-bottom: 25px;
    }
    h3 {
      font-size: 16px;
      line-height: 24px;
    }
    &__question {
      padding-left: 50px;
      padding-right: 50px;
      p {
        font-size: 24px;
        line-height: 34px;
      }
    }
    &__form-bg {
      left: 59%;
      transform: translateX(-50%);
    }
  }
}

@media screen and (max-width: 600px) {
  .q-block {
    .container {
      padding-top: 60px;
    }
    h2 {
      font-size: 20px;
      line-height: 30px;
      margin-bottom: 18px;
    }
    h3 {
      font-size: 12px;
      line-height: 20px;
      margin-bottom: 40px;
    }
    &__question {
      padding-left: 20px;
      padding-right: 20px;
      p {
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 15px;
      }
    }
    &__status {
      font-size: 12px;
      line-height: 20px;
      margin-bottom: 15px;
    }
    label {
      flex-basis: 100%;
    }
    &__next {
      margin: -50px 20px 17px auto;
      right: 0;
      bottom: 22px;
      height: 50px;
      width: 110px;
      font-size: 13px;
    }
    .btn {
      font-size: 12px;
      height: 50px;
    }
    
    &__main-img {
      width: 299px;
      height: 348px;
    }
    &__form {
      margin: 0 auto 250px;
    }
    &__main-info {
      margin: 0 auto 0 30px;
      top: -40px;
      left: 0;
      p,span {
        font-size: 12px;
      }
      p:after {
        width: 147%;
        height: 168%;
      }
      span:after {
        width: 156%;
        height: 265%;
      }
      p {
        margin-top: 6px;
      }
    }
  }
}

@media screen and (max-width: 420px) {
  .q-block {
    &__answers {
      a {
        width: 48%;
        margin: 0 5px;
      }
    }
  }
}