.thx-page {
  overflow-x: visible;

  .header__callback {
    display: none;
  }

  .hero {
    overflow: hidden;

    &:after {
      bottom: -1px;
    }

    height: 855px;

    .container {
      position: relative;

      &>img {
        position: absolute;
        left: 10%;
        bottom: -110px;
      }
    }

    h2 {
      &:after {
        display: none;
      }
    }
  }

  &__inner {
    max-width: 430px;
    margin-left: auto;
    margin-right: 15%;

    &>p {
      color: #353535;
      font-family: "BerlingskeSlab-Md";
      font-size: 22px;
      font-weight: 400;
      line-height: 32px;
      letter-spacing: 0.44px;
      display: flex;
      align-items: center;
      max-width: 362px;
      margin-bottom: 45px;

      img {
        margin-right: 16px;
      }
    }
  }

  &__info {
    position: relative;
    color: #535353;
    font-family: "BerlingskeSlab-Md";
    font-size: 18px;
    font-weight: 400;
    line-height: 28px;
    letter-spacing: 0.36px;
    margin-left: 17px;
    max-width: 350px;
    margin-bottom: 43px;

    span {
      font-family: "BerlingskeSlab-XBd";
    }
  }

  .footer {
    // height: 15vh;
    background-image: none;
    padding-top: 20px;
    padding-bottom: 20px;

    .container {
      display: flex;
      justify-content: space-between;
    }
  }

  h2 {
    color: #2a2a2a;
    font-size: 30px;
    font-weight: 400;
    line-height: 40px;
    font-family: "BerlingskeSlab-XBd";
    letter-spacing: 0.6px;
    text-align: left;
    margin-top: 40px;

    span {
      font-family: "BerlingskeSlab-DBdItalic";
      display: block;
    }
  }

  &__by {
    display: flex;
    align-items: center;

    &>img {
      width: 183px;
    }
  }

  &__by-wrapper {
    display: flex;
    flex-direction: column;
    color: #1c1c1c;

    span:first-of-type {
      font-family: "BerlingskeSlab-MdItalic";
      font-size: 20px;
      letter-spacing: 0.4px;
      line-height: 35px;
    }

    span:nth-of-type(2) {
      font-family: "BerlingskeSlab-XBd";
      line-height: 31px;
      font-size: 22px;
      letter-spacing: 0.4px;
    }

    span:nth-of-type(3) {
      font-family: "BerlingskeSlab-MdItalic";
      letter-spacing: 0.36px;
      line-height: 35px;
      font-size: 18px;
    }


  }
}

@media screen and (max-width: 1240px) {
  .thx-page .hero .container>img {
    left: 0;
  }
}

@media screen and (max-width: 1140px) {
  .thx-page {
    &__inner {
      margin-right: 5%;
    }
  }
}

@media screen and (max-width: 992px) {
  .thx-page {
    .hero {
      min-height: calc(100vh - 103px);
      padding-top: 150px;
    }

    .hero .container>img {
      display: none;
    }

    &__inner {
      max-width: 420px;

      margin: auto;
    }
  }
}

@media screen and (max-width: 768px) {
  .thx-page {
    .hero {
      // height: auto;
    }

    .footer {
      .container {
        padding: 0 10px;
      }

      &__deployed {
        margin-bottom: 0;
      }

      &__info {
        text-align: left;
        margin: 0;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .thx-page {
    .hero {
      padding-top: 300px;
    }

    .footer {
      .container {
        padding: 0 10px;
        flex-direction: column;
        align-items: center;
        text-align: center;
      }

      &__deployed {
        margin-bottom: 30px;
        margin-top: 30px;
      }

      &__info {
        text-align: center;
        margin: 0;
      }
    }

    h2 {
      text-align: center;
      font-size: 20px;
      line-height: 30px;
    }

    &__inner {
      &>p {
        font-size: 16px;
        line-height: 22px;
      }
    }

    &__info {
      font-size: 12px;
      line-height: 20px;

    }
    .arrow-left:after {
      top: 6px;
    }
    &__by-wrapper {
      span {
        font-size: 14px!important;
        line-height: 24px;
        white-space: nowrap;
      }
    }
    &__by {
      & > img {
        width: auto;
      }
    }
  }
}

